import React, { useState, useRef, useEffect } from "react";
import { Link, useParams, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';
import { styled } from '@mui/material/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedinIn, faFacebookF, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faLink } from '@fortawesome/fontawesome-free-solid'

import Video from "../../components/Video";
import Forumitem from "../../components/University/Forumitem";
import Pagination from "../../components/University/Pagination";

import styles from './InstructorInfo.module.scss';
import { getImg } from "../../utils/Helper";

import {
	changePage,
	startAction,
	endAction,
	showToast,
	chatting
} from '../../actions/common'
import { logout } from "../../actions/auth";
import agent from '../../api/'

const StyledBadge = styled(Badge)(({ theme }) => ({
	'& .MuiBadge-badge': {
		left: 144,
		top: 31,
		border: 'none',
		padding: '0',
		backgroundColor: 'transparent',
		transform: `scale(1) translate(0, -41%)`
	},
}));

const contentsMenu =
	[
		'Todos os cursos',
		'Marketing e Vendas',
		'Treinamento de Vendas',
		'Endomarketing',
		'Gestão de Tempo',
		'Gestão Financeira',
		'Digitação',
		'Produtividade',
		'Design Thinking'
	];

const img = <img className={styles.active_icon} src={getImg('image/icons/forum_active.png')} alt="avatar" />

const InstructorInfo = (props) => {
	const auth = useSelector(state => state.auth)
	const navigate = useNavigate()
	const dispatch = useDispatch()

	const [moreview, setMoreview] = useState(false);
	const [forumShow, setForumShow] = useState(3);
	const [forumAllShow, setForumAllShow] = useState(false);
	const [paginationShowCount, setPaginationShowCount] = useState(0);
	const [instructorDetail, setInstructorDetail] = useState({});
	const [instructorCourses, setInstructorCourse] = useState([]);
	const [forums, setForum] = useState([]);
	const contentRef = useRef(null);

	const moreView = () => {
		setForumShow((oldForumShow) => oldForumShow >= forums.length ? oldForumShow : oldForumShow + 3)
		if (forumShow >= forums.length) {
			setForumAllShow(true);
		}
	}

	const getCount = () => {
		const contentWidth = contentRef.current?.offsetWidth;
		const count = Math.floor(contentWidth / 230);
		setPaginationShowCount(count);
	}

	const updateStatus = async (idx, sta) => {
		dispatch(startAction())
		const res = await agent.common.updateEnjoy(auth.currentUser.id, forums[idx].id, sta)
		if (res.data.success) dispatch(showToast('success', res.data.message))
		else dispatch(showToast('error', res.data.message))
		dispatch(endAction())
		let newArray = forums;
		newArray[idx].status = sta
		setForum([...newArray])
	}

	useEffect(() => {
		const getData = async () => {
			dispatch(startAction())
			try {
				const resInstructorDetail = await agent.common.getInstructorsDetail(Number(props.id))
				const resInstructorCourse = await agent.common.getCourseByInstructor(Number(props.id))
				// const resForum = await agent.common.getForumByCourse(Number(props.id))
				if (resInstructorDetail.data.success) setInstructorDetail({ ...resInstructorDetail.data.data })
				if (resInstructorCourse.data.success) setInstructorCourse([...resInstructorCourse.data.data])
				// if (resForum.data.success) setForum([...resForum.data.data])
				dispatch(endAction())
			} catch (error) {
				if (error.response.status >= 400 && error.response.status <= 415) {
					dispatch(endAction())
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
				}
			}
		}
		getData()
	}, [])

	return (
		<div className={styles.university_content}>
			<div className={styles.tab_btn_container}>
				<div className={styles.tab_btn}>
					<button className={`${styles.tab_first_btn} ${styles.r_back}`} onClick={() => dispatch(changePage('university'))}>Conteúdos</button>
					<button className={`${styles.tab_second_btn} ${styles.w_back}`}>Instrutores</button>
				</div>
			</div>
			<div className={styles.instructor_info}>

				<div className={styles.header}>
					<div className={styles.title}>
						Instrutores
					</div>
					<div className={styles.subtitle}>
						<div onClick={() => dispatch(changePage('instructor'))}>
							Voltar
						</div>
					</div>
				</div>
				<div className={styles.info_container} ref={contentRef} onLoad={() => getCount()}>
					<div className={styles.left_info}>
						<div className={styles.avatar}>
							{
								(instructorDetail.activate) ?
									<StyledBadge badgeContent={img}>
										<img className={styles.active} src={instructorDetail.avatarImg} alt="avatar" />
									</StyledBadge>
									:
									<div className={styles.avatarImg}>
										<img src={instructorDetail.avatarImg} alt="avatar" />
									</div>
							}
						</div>
						<div className={styles.online_status}>
							{
								(instructorDetail.onlineStatus) ?
									<div className={styles.online}>
										<span>
											Online
										</span>
									</div>
									:
									<div className={styles.offline}>
										<span>
											Offline
										</span>
									</div>
							}
						</div>
						{/* <div className={styles.social_btn_container}>
							<div className={styles.social_btn}>
								<FontAwesomeIcon icon={faLink} />
							</div>
							<div className={styles.social_btn}>
								<FontAwesomeIcon icon={faFacebookF} />
							</div>
							<div className={styles.social_btn}>
								<FontAwesomeIcon icon={faInstagram} />
							</div>
							<div className={styles.social_btn}>
								<FontAwesomeIcon icon={faLinkedinIn} />
							</div>
							<div className={styles.social_btn}>
								<img className={styles.team_icon} src={getImg('image/icons/teams.png')} alt="icon" />
							</div>
						</div> */}
						<div className={styles.social_btn_container}>
							<div className={styles.social_btn}>
								{
									instructorDetail.facebook !== '' ?
										<a href={instructorDetail.facebook} target='_blank'>
											<img src={getImg('image/icons/facebook.png')} alt="icon" />
										</a> :
										<a>
											<img src={getImg('image/icons/facebook.png')} alt="icon" />
										</a>
								}
							</div>
							<div className={styles.social_btn}>
								{
									instructorDetail.instagram !== '' ?
										<a href={instructorDetail.instagram} target='_blank'>
											<img src={getImg('image/icons/instagram.png')} alt="icon" />
										</a> :
										<a>
											<img src={getImg('image/icons/instagram.png')} alt="icon" />
										</a>
								}
							</div>
							<div className={styles.social_btn}>
								{
									instructorDetail.linkedin !== '' ?
										<a href={instructorDetail.linkedin} target='_blank'>
											<img src={getImg('image/icons/linkedin.png')} alt="icon" />
										</a> :
										<a>
											<img src={getImg('image/icons/linkedin.png')} alt="icon" />
										</a>
								}
							</div>
							<div className={styles.social_btn}>
								{
									instructorDetail.twitter !== '' ?
										<a href={instructorDetail.twitter} target='_blank'>
											<img src={getImg('image/icons/twiter.png')} alt="icon" />
										</a> :
										<a>
											<img src={getImg('image/icons/twiter.png')} alt="icon" />
										</a>
								}
							</div>
						</div>
					</div>
					<div className={styles.right_info}>
						<div className={styles.header}>
							<div className={styles.title}>
								{instructorDetail.name}
							</div>
							<div className={styles.subtitle}>
								{contentsMenu[instructorDetail.type]}
							</div>
						</div>
						<div className={styles.more_info}>
							<div className={styles.students}>
								<div className={styles.icon}>
									<img src={getImg('image/icons/users.png')} alt="icon" />
								</div>
								<div className={styles.content}>
									<div className={styles.title}>Total de Alunos</div>
									<div className={styles.count}>{instructorDetail.studentCount}</div>
								</div>
							</div>
							<div className={styles.course}>
								<div className={styles.icon}>
									<img src={getImg('image/icons/total_video.png')} alt="icon" />
								</div>
								<div className={styles.content}>
									<div className={styles.title}>Total de Cursos</div>
									<div className={styles.count}>{instructorDetail.courseCount}</div>
								</div>
							</div>
							<div className={styles.chat_btn} onClick={() => dispatch(chatting(true))}>
								<div className={styles.icon}>
									<img src={getImg('image/icons/start_chat_btn.png')} alt="icon" />
								</div>
								Iniciar Chat
							</div>
						</div>
						<div className={styles.description}>
							<div className={styles.title}>
								Sobre
							</div>
							<div className={(moreview ? styles.all_content : styles.content)}>
								{instructorDetail.about}
							</div>
							<div className={styles.more_view}>
								<button onClick={() => setMoreview((oldMoreview) => oldMoreview ? false : true)} >Mostrar mais</button>
							</div>
						</div>
					</div>
				</div>
				<div className={styles.courses}>
					<div className={styles.title}>
						Cursos({instructorCourses.length})
					</div>
					<div className={styles.content}>
						<Pagination items={instructorCourses} count={paginationShowCount} rows={['', '']} type={1} />
					</div>
				</div>

				{/* <div className={styles.forum}>
					<div className={styles.forum_title}>
						Fórum
					</div>
					<div className={styles.forum_container}>
						{forums.map((forum, idx) => {
							if (idx < forumShow) {
								if (idx === forumShow - 1 && !forumAllShow) {
									return <Forumitem value={forum} lastview={true} key={idx} index={idx} updateStatus={updateStatus} />
								} else {
									return <Forumitem value={forum} lastview={false} key={idx} index={idx} updateStatus={updateStatus} />
								}
							}
						})}
						{
							!forumAllShow &&
							<button onClick={() => moreView()} className={styles.more_view_btn}>Mostrar mais</button>
						}
					</div>
				</div> */}

			</div >
		</div>
	)
}

export default InstructorInfo