import axios from 'axios'

import { API_URL, REAL_API_URL } from '../constants/'

const authHeader = {
  headers: {
    'Authorization': 'Bearer ' + localStorage.getItem('token')
  }
}

const authApi = {
  login: (email, password) => axios.post(`${REAL_API_URL}/auth/login?email=` + email + `&password=` + password),

  register: (email, password) => axios.post(`${API_URL}/auth/register`, {
    email,
    password
  }),

  me: () => axios.get(`${API_URL}/users`, authHeader),

  renewToken: () => axios.post(`${API_URL}/users/renewToken`, {}, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  getAccountImg: (userId) => axios.post(`${API_URL}/getAccountImg`, { userId }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  getBioData: (userId) => axios.post(`${API_URL}/getBioData`, { userId }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  getAccountData: (userId) => axios.post(`${API_URL}/getAccountData`, { userId }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  getCargoList: () => axios.get(`${API_URL}/responsability`, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  updatePassword: (userId, password) => axios.post(`${API_URL}/updatePassword`, { userId, password }),

  updateBio: (userId, name, bio) => axios.post(`${API_URL}/updateBio`, {
    userId,
    name,
    bio
  }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  updateAccount: (userId, email, role, password, facebook, instagram, twitter, linkedin) => axios.post(`${API_URL}/updateAccount`, {
    userId,
    email,
    role,
    password,
    facebook,
    instagram,
    twitter,
    linkedin
  }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),

  sendScurityCode: (userId, code) => axios.post(`${API_URL}/sendScurityCode`, {
    userId,
    code
  }, {
    headers: {
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    }
  }),
}

export default authApi