import React, { useEffect, useState, useRef } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate } from 'react-router'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'
import CloseIcon from '@mui/icons-material/Close';
import Menuitem from "./Menuitem"
import Notification from "./Notification"

import styles from './Sidemenu.module.scss'
import { getImg, useWindowSize } from "../../utils/Helper"

import agent from '../../api/'

import {
	changePage,
	startAction,
	endAction,
	showToast,
	showSmall,
	isLive
} from '../../actions/common'
import { logout, createCharacter } from '../../actions/auth'

const menulists = [
	{ id: 0, img: 'image/icons/Universe.png', text: 'Universo', page: 'home' },
	{ id: 1, img: 'image/icons/live.png', text: 'Palestra ao Vivo', page: '' },
	{ id: 2, img: 'image/icons/university.png', text: 'Biblioteca', page: 'university' },
	{ id: 3, img: 'image/icons/progress.png', text: 'Progresso', page: 'progress' },
	{ id: 4, img: 'image/icons/schedule.png', text: 'Agenda', page: 'schedule' },
	{ id: 5, img: 'image/icons/ranking.png', text: 'Ranking', page: 'ranking' },
	{ id: 6, img: 'image/icons/customize.png', text: 'Customização', page: '' },
	{ id: 7, img: 'image/icons/relative.png', text: 'Relatório', page: 'report' },
	{ id: 8, img: 'image/icons/notification.png', text: 'Notificações', page: '' },
	{ id: 9, img: 'image/icons/logout.png', text: 'Logout', page: '' }
]

let menuWidth = 0;
let intervalID = 0;

const Sidemenu = (props) => {
	const navigate = useNavigate()
	const dispatch = useDispatch()
	const auth = useSelector(state => state.auth)
	const isSmall = useSelector(state => state.common.showSmall)

	const [showNotification, setShowNotification] = useState(false);
	const [notification, setNotification] = useState(0);
	const [notificationData, setNotificationData] = useState([]);
	const [image0, setImage0] = useState('');
	const [showConfirmMsg, setShowConfirmMsg] = useState(false)

	const [windowWidth, windowHeight] = useWindowSize();

	useEffect(() => {
		const getUserData = async () => {
			try {
				const resNotificationData = await agent.common.getNotification()
				if (resNotificationData.data.success) {
					let notificationArray = []
					resNotificationData.data.data.map((notUser) => {
						notificationArray.push({
							id: notUser.id,
							title: notUser.title,
							description: notUser.message,
							type: notUser.type,
							link: "",
							viewed: notUser.read,
						})
					})

					setNotificationData([...notificationArray])
					let count = 0;
					notificationArray.map((alarm) => {
						if (!alarm.viewed) {
							count++;
						}
					})
					setNotification(count)
				}
			} catch (error) {
				if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
					clearInterval(intervalID);
					// console.log("interval id: ", intervalID)
					// intervalID = -1
				}
			}
		}

		const getAdminData = async () => {
			try {
				const resNotificationData = await agent.common.notification()
				const resNotificationUserData = await agent.common.notificationUser()
				if (resNotificationData.data.success && resNotificationUserData.data.success) {
					let notificationArray = []
					resNotificationUserData.data.data.map((notUser) => {
						if(notUser.id_user === auth.currentUser.id){
							for(let i=0; i<resNotificationData.data.data.length; i++) {
								if(notUser.id_notification === resNotificationData.data.data[i].id) {
									let type
									switch(resNotificationData.data.data[i].type) {
										case "LESSON":
											type = 1
											break
										case "MEETING":
											type = 2
											break
										case "INTERACTION":
											type = 3
											break
									}
									notificationArray.push({
										id: notUser.id_notification,
										title: resNotificationData.data.data[i].title,
										description: resNotificationData.data.data[i].description,
										type: type,
										link: resNotificationData.data.data[i].link,
										viewed: notUser.viewed,
									})
								}
							}
						}
					})

					setNotificationData([...notificationArray])
					let count = 0;
					notificationArray.map((alarm) => {
						if (!alarm.viewed) {
							count++;
						}
					})
					setNotification(count)
				}
			} catch (error) {
				if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
					dispatch(showToast('error', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
					clearInterval(intervalID);
					// console.log("interval id: ", intervalID)
					// intervalID = -1
				}
			}
		}

		if(auth.currentUser.role === 'ADMIN') {
			getAdminData()
		} else {
			getUserData()
		}

		// intervalID = setInterval(function () {
		// 	getData()
		// 	clearInterval(intervalID);
		// }, 10000);
	}, [])

	useEffect(() => {
		let modelData = auth.characterData
		if (modelData.gendor !== -1) {
			const imageData = localStorage.getItem('avatar0_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin)
			if (imageData != undefined && imageData != null)
				setImage0(imageData)
		}
	}, [auth.characterData])

	useEffect(() => {
		if (isSmall) {
			menuWidth = 100;
			props.getWidth(menuWidth);
		} else {
			menuWidth = 266;
			props.getWidth(menuWidth);
		}
	}, [isSmall])

	const setNtf = (num) => {
		setNotification(num)
	}

	const toggleMenu = () => {
		(isSmall ? dispatch(showSmall(false)) : dispatch(showSmall(true)));
	}

	const toggleNotification = () => {
		setShowNotification((old) => old ? false : true)
	}

	const closeInterval = () => {
		return clearInterval(intervalID);
	}

	const getCustomizeData = async () => {
		try {
			const res = await agent.common.getCustomizeModel(auth.currentUser.id)
			if (res.data.success) {
				dispatch(changePage('none'))
				dispatch(createCharacter(res.data))
				window.dragon.clickCustomize(res.data.data)
			}
		} catch (error) {
			if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
				dispatch(showToast('error', error.response.data.message))
				if (error.response.data.message == 'Unauthorized') {
					localStorage.removeItem('token')
					dispatch(logout())
					window.dragon.stopUnityGame()
					dispatch(changePage('login'))
				}
			}
		}
	}

	const goLectureRoom = () => {
		dispatch(changePage('play'))
		window.dragon.clickPlay(auth.currentUser.role)
		dispatch(isLive(true))
	}

	const logoutClick = () => {
		setShowConfirmMsg(true)
	}

	const logoutConfirm = () => {
		localStorage.removeItem('token')
		dispatch(logout())
		window.dragon.stopUnityGame()
		dispatch(changePage('login'))
	}



	window.dragon.saveImage0 = function (data, gendor, hair, face, clothes, shoes, skin) {
		localStorage.setItem('avatar0_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
		setImage0(encodeURIComponent(data))
	}

	return (
		<>
			<div className={(isSmall ? styles.small_sidemenu : styles.sidemenu)} style={{ height: windowHeight + 'px' }} >
				{isSmall &&
					<div className={styles.sidemenu_container}>
						<div className={styles.sidemenu_small_header}>
							{
								image0 === '' ?
									<img className={styles.sidemenu_small_header_img} src={getImg('image/back/home_small.png')} alt="Logo" />
									:
									<img className={styles.sidemenu_small_header_img} src={"data:image/png;base64," + image0} alt="Logo" />
							}
							<div className={styles.sidemenu_small_header_card}>
							</div>
						</div>
						<div className={styles.sidemenu_small_content}>
							{menulists.map((menulist, idx) => {
								if ((auth.currentUser.role === "ADMIN" && idx === 7) || idx !== 7) {
									if (props.active === idx) {
										return <Menuitem value={menulist} logoutClick={logoutClick} closeInterval={closeInterval} goLectureRoom={goLectureRoom} getCustomizeData={getCustomizeData} notification={notification} small={true} active={true} key={idx} />
									} else {
										return <Menuitem value={menulist} logoutClick={logoutClick} closeInterval={closeInterval} goLectureRoom={goLectureRoom} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={true} active={false} key={idx} />
									}
								}
							})}
						</div>
					</div>
				}

				{!isSmall &&
					<div className={styles.sidemenu_container}>
						<div className={styles.sidemenu_header}>
							<div className={styles.sidemenu_header_effect_img_container}>
								<img className={styles.sidemenu_header_effect_img} src={getImg('image/back/effect.png')} alt="Logo" />
							</div>
							<div className={styles.sidemenu_header_img_container}>
								{
									image0 === '' ?
										<img className={styles.sidemenu_header_img} src={getImg('image/back/home_small.png')} alt="Logo" />
										:
										<img className={styles.sidemenu_header_img} src={"data:image/png;base64," + image0} alt="Logo" />
								}
							</div>
							<div className={styles.sidemenu_header_card_container}>

								<div className={styles.sidemenu_header_card}>
									<div className={styles.sidemenu_header_card_back}>
										<div className={styles.sidemenu_header_card_text}>
											{auth.currentUser.firstname}
										</div>
									</div>
								</div>
							</div>
							<div className={styles.sidemenu_header_btn_container}>
								<button className={styles.sidemenu_header_btn} onClick={() => dispatch(changePage('profile'))}>Perfil</button>
							</div>
						</div>

						<div className={styles.sidemenu_content}>
							{menulists.map((menulist, idx) => {
								if ((auth.currentUser.role === "ADMIN" && idx === 7) || idx !== 7) {
									if (props.active === idx) {
										return <Menuitem value={menulist} logoutClick={logoutClick} closeInterval={closeInterval} goLectureRoom={goLectureRoom} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={false} active={true} key={idx} />
									} else {
										return <Menuitem value={menulist} logoutClick={logoutClick} closeInterval={closeInterval} goLectureRoom={goLectureRoom} getCustomizeData={getCustomizeData} toggleNotification={toggleNotification} notification={notification} small={false} active={false} key={idx} />
									}
								}
							})}
						</div>
					</div>
				}

				{isSmall &&
					<div className={styles.sidemenu_small_btn_container}>
						<button onClick={toggleMenu} className={styles.sidemenu_small_btn}><ArrowForwardIosIcon sx={{ fontSize: 16 }} /></button>
					</div>
				}

				{!isSmall &&
					<div className={styles.sidemenu_small_btn_container}>
						<button onClick={toggleMenu} className={styles.sidemenu_small_btn}>< ArrowBackIosIcon sx={{ fontSize: 16 }} /></button>
					</div>
				}

				{
					showNotification && <Notification alarms={notificationData} toggleNotification={toggleNotification} />
				}
			</div>
			{
				showConfirmMsg &&
				<div className={styles.confirm_msg}>
					<div className={styles.rel_container}>
						<div className={styles.box}>
							<div className={styles.box_rel_container}>
								<div className={styles.text}>
								Você está saindo do metaverso llider KOF.
								</div>
								<div className={styles.button_container} onClick={() => logoutConfirm()}>
									<div className={styles.button}>
										Ok
									</div>
								</div>
								<div className={styles.close_button} onClick={() => setShowConfirmMsg(false)}>
									<CloseIcon />
								</div>
							</div>
						</div>
					</div>
				</div>
			}
		</>
	)
}

export default Sidemenu