import React, { useEffect, useRef, useState } from "react";
import moment from 'moment'


import styles from './CertificationAccItem.module.scss';
import { getImg } from "../../../utils/Helper";

function CertificationAccItem(props) {

	return (
		<div className={styles.accordion}>
			{
				props.value.map((item, idx) =>
					<div className={styles.main} key={idx}>
						<div className={styles.medal_img_container}>
							<div className={styles.medal_img}>
								<img src={getImg('image/icons/certification_medal.png')} alt="medal" />
							</div>
						</div>
						<div className={styles.text}>
							<div className={styles.title}>
								{item.title}
							</div>
							<div className={styles.description}>
								Concluído em {moment(new Date(item.date)).format('DD/MM/YYYY')}
							</div>
						</div>
					</div>
				)}
		</div>
	);
}

export default CertificationAccItem;
