import React, { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Link, useParams, useNavigate } from 'react-router-dom';

import Video from "../../components/Video";
import Accordion from "../../components/Accordion";
import Test from "../../components/Test";
import CourseComplete from "../../components/CourseComplete";
import Progress from "../../components/Progress";
import CourseItem from "../../components/AccordionItem/CourseItem";
import Pagination from "../../components/University/Pagination";

import styles from './UniversityContent.module.scss';

import {
	changePage,
	startAction,
	endAction,
	showToast,
	changePageId
} from '../../actions/common'
import { logout } from "../../actions/auth"
import agent from '../../api/'

const UniversityContent = (props) => {
	const auth = useSelector(state => state.auth)
	const dispatch = useDispatch()
	const navigate = useNavigate()

	const [paginationShowCount, setPaginationShowCount] = useState(0);
	const [courseClass, setCourseClass] = useState({});
	const [classes, setClass] = useState([]);
	const [relativeCourses, setRelativeCourse] = useState([]);
	const [isPause, setIsPause] = useState(false);
	const [videoTime, setVideoTime] = useState(0);
	const [notUpdate, setNotUpdate] = useState(false);
	const [progress, setProgress] = useState(0);
	const [isTest, setIsTest] = useState(false);

	const contentRef = useRef(null);

	const getCount = () => {
		const contentWidth = contentRef.current?.offsetWidth;
		const count = Math.floor(contentWidth / 230);
		setPaginationShowCount(count);
	}

	const checkVideo = async (courseIdx, lessonIdx, lessonId) => {

		dispatch(startAction())
		const res = await agent.common.setMarkVideo(auth.currentUser.id, lessonId)
		if (res.data.success) dispatch(showToast('success', res.data.message))
		else dispatch(showToast('error', res.data.message))
		dispatch(endAction())

		let newArray = classes
		newArray[courseIdx].classdata[lessonIdx].status = newArray[courseIdx].classdata[lessonIdx].status ? 0 : 1
		setClass([...newArray])

		let totalCount = 0
		let seeCount = 0

		newArray.map((resclass) => {
			resclass.classdata.map((item) => {
				totalCount++
				if (item.status === 1) {
					seeCount++
				}
			})
		})
		setProgress((seeCount / totalCount).toFixed(2) * 100)

	}

	const changeVideo = (video) => {
		let _courseClass = courseClass
		_courseClass.lastVideo = video

		_courseClass.lastTime = 0

		setCourseClass({ ..._courseClass })
	}

	const goPage = (page, id) => {
		dispatch(changePage(page))
		dispatch(changePageId(id))
	}

	useEffect(() => {
		const getData = async () => {
			dispatch(startAction())
			try {
				const resCourseClass = await agent.common.getCourseClass(auth.currentUser.id, Number(props.id))
				const resClasses = await agent.common.getClasses(Number(props.id))
				const resRelativeCourse = await agent.common.getRelativeCourse(Number(props.id))
				if (resCourseClass.data.success) {
					if (resCourseClass.data.data.lastTime == 100) {
						setNotUpdate(true)
					} else {
						setNotUpdate(false)
					}
					setCourseClass({ ...resCourseClass.data.data })
				}
				if (resClasses.data.success) {
					let totalCount = 0
					let seeCount = 0
					setClass([...resClasses.data.data])
					resClasses.data.data.map((classItem) => {
						classItem.classdata.map((item) => {
							totalCount++
							if (item.status === 1) {
								seeCount++
							}
						})
					})
					setProgress((seeCount / totalCount).toFixed(2) * 100)
				}
				if (resRelativeCourse.data.success) setRelativeCourse([...resRelativeCourse.data.data])
				dispatch(endAction())
			} catch (error) {
				if (error.response.status >= 400 && error.response.status <= 415) {
					dispatch(endAction())
					dispatch(showToast('Failed', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
				}
			}
		}
		getData()
	}, [])

	useEffect(() => {
		async function setPauseTime() {
			try {
				const resModifyLastTime = await agent.common.setLastTime(courseClass.videoId, videoTime, auth.currentUser.id)
				if (resModifyLastTime.data.success) dispatch(showToast('success', resModifyLastTime.data.message))
			} catch (error) {
				if (error.response.status >= 400 && error.response.status <= 415) {
					dispatch(showToast('Failed', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
				}
			}
		}

		if (isPause && !notUpdate) {
			setPauseTime()
		}
	}, [isPause])

	return (
		<div className={styles.university_content}>
			<div className={styles.tab_btn_container}>
				<div className={styles.tab_btn}>
					<button className={`${styles.tab_first_btn} ${styles.w_back}`}>Conteúdos</button>
					<button className={`${styles.tab_second_btn} ${styles.r_back}`} onClick={() => dispatch(changePage('instructor'))}>Instrutores</button>
				</div>
			</div>
			<div className={styles.university_content_content}>
				<div className={styles.content_header}>
					<div className={styles.title}>
						Conteúdos
					</div>
					<div className={styles.subtitle}>
						<div onClick={() => goPage('detail', props.id)}>
							Voltar
						</div>
					</div>
				</div>
				<div className={styles.video} ref={contentRef} onLoad={() => getCount()} onLoadedData={() => getCount()}>
					<div className={styles.video_header}>
						<div className={styles.title}>
							{courseClass.title}
						</div>
						<div className={styles.subtitle}>
							{courseClass.subTitle}
						</div>
					</div>
					<div className={styles.video_container}>
						<Video id={courseClass.videoId} setIsPause={setIsPause} setVideoTime={setVideoTime} value={{ video: courseClass.lastVideo, lastTime: courseClass.lastTime }} width={905} />
					</div>
				</div>
				<div className={styles.progress}>
					<div className={styles.progress_container}>
						<div className={styles.container}>
							<Progress back={`#EB534C`} bar={`#F5C242`} value={progress} />
						</div>
					</div>
					<div className={styles.text}>
						{progress >= 0 ? progress : 0} de 100 aulas completas
					</div>
					{
						progress === 100 && !localStorage.getItem("courseTest" + auth.currentUser.id + "-" + props.id) &&
						<div className={styles.test_btn}>
							<button onClick={() => setIsTest(true)}>Test</button>
						</div>
					}
				</div>
				{
					classes.length > 0 &&
					<div className={styles.course}>
						<div className={styles.title}>
							Conteúdo do curso
						</div>
						<div className={styles.course_container}>
							{classes.map((item, idx) =>
								<Accordion value={item} key={idx} content={<CourseItem value={item.classdata} courseId={idx} checkVideo={checkVideo} changeVideo={changeVideo} />} />
							)}
						</div>
					</div>
				}
				{
					relativeCourses.length > 0 &&
					<div className={styles.relate}>
						<div className={styles.title}>
							Cursos relacionados
						</div>
						<div className={styles.relate_container}>
							<Pagination items={relativeCourses} count={paginationShowCount} rows={['']} type={1} />
						</div>
					</div>
				}

				{
					isTest && <Test courseId={Number(props.id)} />
				}
			</div >
		</div >
	)
}

export default UniversityContent