import {
  CHANGE_PAGE,
  CHANGE_PAGE_ID,
  START_GAME,
  START_ACTION,
  END_ACTION,
  SHOW_TOAST,
  HIDE_TOAST,
  SHOW_CHAT,
  CHATTING,
  SHOW_SMALL,
  IS_LIVE
} from '../constants/actionTypes/common'

const INITIAL_STATE = {
  page: 'login',
  pageId: 0,
  loadLogin: false,
  loading: false,
  showToast: false,
  msgType: '',
  msgContent: '',
  showChat: false,
  chatting: false,
  showSmall: false,
  isLive: false
}

const reducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case CHANGE_PAGE:
      return {
        ...state,
        page: action.page
      }
    case CHANGE_PAGE_ID:
      return {
        ...state,
        pageId: action.pageId
      }
    case START_GAME:
      return {
        ...state,
        loadLogin: true
      }
    case START_ACTION:
      return {
        ...state,
        loading: true
      }
    case END_ACTION:
      return {
        ...state,
        loading: false
      }
    case SHOW_TOAST:
      return {
        ...state,
        showToast: true,
        msgType: action.msgType,
        msgContent: action.content
      }
    case HIDE_TOAST:
      return {
        ...state,
        showToast: false
      }
    case SHOW_CHAT:
      return {
        ...state,
        showChat: action.isShow
      }
    case CHATTING:
      return {
        ...state,
        chatting: action.isChat
      }
    case SHOW_SMALL:
      return {
        ...state,
        showSmall: action.isSmall
      }
    case IS_LIVE:
      return {
        ...state,
        isLive: action.isLive
      }
    default:
      return state
  }
}

export default reducer