import React from "react";

import styles from './Message.module.scss';
import { getImg } from "../../../utils/Helper";

const Message = (props) => {
	return (
		<div>
			{props.value.type === 'send' &&
				<div className={styles.send}>
					<div className={styles.send_container}>
						<div className={styles.message_header}>
							<img className={styles.message_header_img} src={getImg(`${props.value.img}`)} alt="avatar" />
							<div className={styles.message_header_name}>
								{props.value.name}
							</div>
						</div>
						<div className={styles.message_content}>
							<div className={styles.message}>
								{props.value.chat}
								<div className={styles.time}>
									{props.value.time}
								</div>
							</div>
							<div className={styles.triangle}></div>
						</div>
					</div>
				</div>
			}
			{props.value.type === 'receive' &&
				<div className={styles.receive}>
					<div className={styles.receive_container}>
						<div className={styles.message_header}>
							<img className={styles.message_header_img} src={getImg(`${props.value.img}`)} alt="avatar" />
							<div className={styles.message_header_name}>
								{props.value.name}
							</div>
						</div>
						<div className={styles.message_content}>
							<div className={styles.message}>
								{props.value.chat}
								<div className={styles.time}>
									{props.value.time}
								</div>
							</div>
							<div className={styles.triangle}></div>
						</div>
					</div>
				</div>
			}
		</div>
	)
}

export default Message