import React, { useEffect, useState, useRef } from "react";

import styles from './RankingItem.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";


// let rankArray = [];

const RankingItem = (props) => {

	const [image, setImage] = useState('')

	useEffect(() => {
		if (props.makeRankingAvatar) {
			if (props.value.customizeInfo != null) {
				setImage("data:image/png;base64," + localStorage.getItem('avatar3_' + props.value.customizeInfo.gendor + props.value.customizeInfo.hair + props.value.customizeInfo.face + props.value.customizeInfo.clothes + props.value.customizeInfo.shoes + props.value.customizeInfo.skin))
			} else {
				setImage(props.value.avatarImg)
			}
		} else {
			setImage(props.value.avatarImg)
		}
	}, [props.makeRankingAvatar])

	return (
		<div className={styles.ranking_item}>
			<div className={styles.number}>
				{props.order}
			</div>
			<div className={`${styles.card_container} ${(props.even && styles.even)} ${(props.me && styles.me)}`} id={`${props.me && 'me'}`}>
				<div className={styles.img_container}>
					<img src={image} alt="Logo" />
				</div>
				<div className={styles.name}>
					{props.value.name}
				</div>
				<div className={styles.unit}>
					{props.value.unit}
				</div>
				<div className={styles.office}>
					{props.value.office}
				</div>
				<div className={styles.time}>
					{props.value.time}
				</div>
				<div className={styles.achievements}>
					{
						Array.from(Array(props.value.achievements).keys()).map((_index) => {
							return <div className={styles.medal_img} key={_index}><img src={getImg('image/icons/rank_medal.png')} alt="Logo" /></div>
						})
					}
				</div>
				<div className={styles.average}>
					{props.value.average}
				</div>

			</div>
		</div>
	)
}

export default RankingItem