import React, { useEffect, useState, useRef } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedinIn, faFacebookF, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faLink } from '@fortawesome/fontawesome-free-solid';
import CloseIcon from '@mui/icons-material/Close';


import styles from './CourseComplete.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";



const CourseComplete = (props) => {
	const auth = useSelector(state => state.auth)
	const [windowWidth, windowHeight] = useWindowSize();

	const [image, setImage] = useState('');
	const [show, setShow] = useState(true);

	useEffect(() => {
		let modelData = auth.characterData
		setImage(localStorage.getItem('avatar4_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin))
	}, [])


	return (
		show &&
		<div className={styles.complete_modal} style={{ width: windowWidth + 'px', height: windowHeight + 'px' }}>
			<div className={styles.card}>
				<div className={styles.close_btn} onClick={() => setShow(false)}>
					<CloseIcon />
				</div>
				{
					image === '' ?
						<img className={styles.modal_img} src={getImg('image/back/course100.png')} alt="Logo" />
						:
						<img className={styles.modal_img} src={"data:image/png;base64," + image} alt="Logo" />
				}
				<div className={styles.main_board}>
					<div className={styles.title}>
						Parabéns!
					</div>
					<div className={styles.content}>
						Você alcançou mais um nível no nosso Coca Verso. Agora você está oficialmente na categoria "Gold".
					</div>
				</div>
				<div className={styles.text}>
					Aproveita e conta para todo mundo da sua nova conquista.
				</div>
				<div className={styles.social_icons}>
					<div className={styles.social_icon}>
						<FontAwesomeIcon icon={faLink} />
					</div>
					<div className={styles.social_icon}>
						<FontAwesomeIcon icon={faFacebookF} />
					</div>
					<div className={styles.social_icon}>
						<FontAwesomeIcon icon={faInstagram} />
					</div>
					<div className={styles.social_icon}>
						<FontAwesomeIcon icon={faLinkedinIn} />
					</div>
					<div className={styles.social_icon}>
						<img src={getImg('image/icons/teams.png')} alt="Logo" />
					</div>
				</div>
			</div>
		</div>
	)
}

export default CourseComplete