import React, { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Link, useNavigate } from "react-router-dom";

import Video from "../../components/Video";
import Pagination from "../../components/University/Pagination";
import Forumitem from "../../components/University/Forumitem";

import styles from './University.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from "../../actions/auth"
import agent from '../../api/'

// const contentsMenu =
//   [
//     'Todos os cursos',
//     'Marketing e Vendas',
//     'Treinamento de Vendas',
//     'Endomarketing',
//     'Gestão de Tempo',
//     'Gestão Financeira',
//     'Digitação',
//     'Produtividade',
//     'Design Thinking'
//   ];
const categorysMenu =
  [
    'Assistidos',
    'Lista de Aulas',
    'Aulas ao Vivo'
  ];

let callCount = 0

const University = (props) => {
  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [contentActive, setContentActive] = useState(0);
  const [categoryActive, setCategoryActive] = useState(1);
  const [forumShow, setForumShow] = useState(3);
  const [paginationShowCount, setPaginationShowCount] = useState(0);
  const [forumAllShow, setForumAllShow] = useState(false);
  const [isPause, setIsPause] = useState(false);
  const [videoTime, setVideoTime] = useState(0);
  const [lastVideo, setLastVideo] = useState({});
  const [contentsMenu, setContentsMenu] = useState([]);
  const [courses, setCourse] = useState([]);
  const [forums, setForum] = useState([]);
  const [oCourses, setOCourse] = useState([]);
  const [oForums, setOForum] = useState([]);
  const [search, setSearch] = useState('');

  const contentRef = useRef(null);


  const moreView = () => {
    setForumShow((oldForumShow) => oldForumShow + 3 >= forums.length ? forums.length : oldForumShow + 3)
    if (forumShow + 3 >= forums.length) {
      setForumAllShow(true);
    }
  }

  const getCount = () => {
    const contentWidth = contentRef.current?.offsetWidth;
    const count = Math.floor(contentWidth / 230);
    setPaginationShowCount(count);
  }

  const updateStatus = async (idx, sta) => {
    dispatch(startAction())
    const res = await agent.common.updateEnjoy(auth.currentUser.id, forums[idx].id, sta)
    if (res.data.success) dispatch(showToast('success', res.data.message))
    else dispatch(showToast('error', res.data.message))
    dispatch(endAction())
    let newArray = forums;

    newArray[idx].status = sta
    setForum([...newArray])
  }

  const getCourse = (c) => {
    let typeArray = [];
    for (let i = 0; i < c.length; i++) {
      let count = 0;
      for (let j = 0; j < typeArray.length; j++) {
        if (c[i].category_id === typeArray[j]) count++
      }
      if (!count) typeArray.push(c[i].category_id)
    }
    let course = []
    for (let k = 0; k < typeArray.length; k++) {
      let typeCourse = []
      c.map((course) => {
        if (course.category_id === typeArray[k]) {
          typeCourse.push(course)
        }
      })
      course.push({ category_id: typeArray[k], data: [...typeCourse] })
    }
    return course
  }

  useEffect(() => {
    async function getData() {
      dispatch(startAction())
      try {
        const resLastVideo = await agent.common.getLastVideo(auth.currentUser.id)
        const resContentMenu = await agent.common.getCourseCategory()
        if (resLastVideo.data.success) setLastVideo({ ...resLastVideo.data.data })
        if (resContentMenu.data.success == true) {
          let contentArray = ["Todos os cursos"]
          resContentMenu.data.data.map((item) => {
            contentArray[item.id] = item.name
          })
          console.log(contentArray)
          setContentsMenu([...contentArray])
        }
        const resCourse = await agent.common.getCourses("" + contentActive, "" + categoryActive)
        // const resForum = await agent.common.getForumByCategory(contentActive)
        // if (resForum.data.success) {
        //   setForum(resForum.data.data)
        //   setOForum(resForum.data.data)
        // }
        if (resCourse.data.success) {
          setCourse([...getCourse(resCourse.data.data)])
          setOCourse([...getCourse(resCourse.data.data)])
        }
        callCount++;
        dispatch(endAction())
      } catch (error) {
        if (error.response != undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('Failed', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }

    getData()
  }, [contentActive, categoryActive])

  useEffect(() => {
    if (search !== '') {
      let newCArray = []
      let cs = oCourses;
      cs.map((c) => {
        c.data.map((item) => {
          if (item.title.search(search) !== -1 || item.content.search(search) !== -1 || `${item.count}`.search(search) !== -1 || `${item.time}`.search(search) !== -1 || item.text.search(search) !== -1) {
            newCArray.push(item)
          }
        })
      })
      setCourse([...getCourse(newCArray)])

    } else {
      setCourse([...oCourses])
    }
  }, [search])

  // useEffect(() => {
  //   async function setPauseTime() {
  //     try {
  //       const resModifyLastTime = await agent.common.setLastTime(lastVideo.id, videoTime, auth.currentUser.id)
  //       if (resModifyLastTime.data.success) dispatch(showToast('success', resModifyLastTime.data.message))
  //     } catch (error) {
  //       if (error.response.status >= 400 && error.response.status <= 415) {
  //         dispatch(showToast('Failed', error.response.data.message))
  //         if (error.response.data.message == 'Unauthorized') {
  //           localStorage.removeItem('token')
  //           dispatch(logout())
  //           window.dragon.stopUnityGame()
  //           dispatch(changePage('login'))
  //         }
  //       }
  //     }
  //   }

  //   if (isPause) {
  //     setPauseTime()
  //   }
  // }, [isPause])


  return (
    <div className={styles.university_content}>
      <div className={styles.tab_btn_container}>
        <div className={styles.tab_btn}>
          <button className={`${styles.tab_first_btn} ${styles.w_back}`}>Conteúdos</button>
          <button className={`${styles.tab_second_btn} ${styles.r_back}`} onClick={() => dispatch(changePage('instructor'))}>Instrutores</button>
        </div>
      </div>
      <div className={styles.university_first_content_container}>
        <div className={styles.university_left_header}>
          <div className={styles.content_menu}>
            <div className={styles.content_menu_title}>
              Conteúdos
            </div>
            <div className={styles.content_menu_list}>
              {contentsMenu.map((content, idx) => {
                if (contentActive === idx) {
                  return <div className={`${styles.menu_item} ${styles.active}`} key={idx}>{content}</div>
                } else {
                  return <div className={styles.menu_item} key={idx} onClick={() => setContentActive(idx)}>{content}</div>
                }
              })}
            </div>
          </div>
          <div className={styles.category_menu}>
            <div className={styles.category_menu_title}>
              Categorias
            </div>
            <div className={styles.category_menu_list}>
              {categorysMenu.map((category, idx) => {
                if (categoryActive === idx + 1) {
                  return <div className={`${styles.menu_item} ${styles.active}`} key={idx}>{category}<div className={styles.active_bar}></div></div>
                } else {
                  return <div className={styles.menu_item} key={idx} onClick={() => setCategoryActive(idx + 1)}>{category}</div>
                }
              })}
            </div>
          </div>
        </div>
        <div className={styles.university_first_content} ref={contentRef} onLoad={() => getCount()} >
          <div className={styles.university_search}>
            <div className={styles.search_input_container}>
              <input className={styles.search_input} value={search} onChange={(e) => setSearch(e.target.value)} placeholder="Pesquisar" />
              <div className={styles.input_icon}>
                <img className={styles.search_icon} src={getImg('image/icons/search_dark.png')} alt="Logo" />
              </div>
            </div>
          </div>
          {
            lastVideo.video != null &&
            <div className={styles.university_video}>
              <Video width={700} id={lastVideo.id} value={lastVideo} setIsPause={setIsPause} setVideoTime={setVideoTime} />
            </div>
          }
          <div className={styles.course_container}>
            {
              courses.length === 0 ?
                <>
                  <div className={styles.height_block}></div>
                  <div className={styles.no_result}>No Result</div>
                </> :
                courses.map((item, idx) => <div className={styles.marketing} key={idx}>
                  <div className={styles.marketing_title}>
                    {contentsMenu[item.category_id]}
                  </div>
                  <div className={styles.marketing_content}>
                    <Pagination items={item.data} count={paginationShowCount} rows={['']} type={1} />
                  </div>
                </div>)
            }
          </div>
          {/* <div className={styles.forum}>
            <div className={styles.forum_title}>
              Fórum
            </div>
            <div className={styles.forum_container}>
              {forums.map((forum, idx) => {
                if (idx < forumShow) {
                  if (idx === forumShow - 1 && !forumAllShow) {
                    return <Forumitem value={forum} lastview={true} key={idx} index={idx} updateStatus={updateStatus} />
                  } else {
                    return <Forumitem value={forum} lastview={false} key={idx} index={idx} updateStatus={updateStatus} />
                  }
                }
              })}
              {
                !forumAllShow &&
                <button onClick={() => moreView()} className={styles.more_view_btn}>Mostrar mais</button>
              }
            </div>
          </div> */}
        </div>
      </div>
    </div>
  )
}

export default University