import React, { useEffect, useRef, useState } from "react";
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

import styles from './AchievementAcc.module.scss';
import { getImg } from "../../utils/Helper";

function AchievementAcc(props) {
	const [isShow, setIsShow] = useState(false);

	const handleClick = () => {
		setIsShow((oldValue) => oldValue ? false : true)
	}

	return (
		<div className={styles.accordion}>
			<div className={styles.main} onClick={() => handleClick()}>
				<div className={styles.main_icon}>
					{
						isShow ?
							<KeyboardArrowUpIcon /> :
							<KeyboardArrowDownIcon />
					}
				</div>
				<div className={styles.medal_img_container}>
					<div className={styles.medal_img}>
						{
							props.value.level === 1 && <img src={getImg('image/icons/black_medal.png')} alt="medal" />
						}
						{
							props.value.level === 2 && <img src={getImg('image/icons/gold_medal.png')} alt="medal" />
						}
						{
							props.value.level === 3 && <img src={getImg('image/icons/silver_medal.png')} alt="medal" />
						}
						{
							props.value.level === 4 && <img src={getImg('image/icons/copper_medal.png')} alt="medal" />
						}
					</div>
				</div>
				<div className={styles.text}>
					<div className={styles.title}>
						{props.value.title}
					</div>
					<div className={styles.description}>
						{props.value.message}
					</div>
				</div>
			</div>
			{isShow &&
				<div className={styles.content}>
					{props.content}
				</div>
			}
		</div>
	);
}

export default AchievementAcc;
