import React, { useState, useEffect, useRef } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import CloseIcon from '@mui/icons-material/Close';

import Sidemenu from "../../components/Sidemenu";
import LineChart from "../../components/LineChart/LineChart";
import ReportCertificationAcc from "../../components/ReportCertificationAcc";
import CertificationAccItem from "../../components/AccordionItem/CertificationAccItem";
import AchievementAcc from "../../components/AchievementAcc";
import AchievementItem from "../../components/AccordionItem/AchievementItem";
import ChatBtn from "../../components/ChatBtn";


import styles from './Report.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'
import { logout } from "../../actions/auth";

const members = [
  { id: 1, type: 'member', img: 'image/back/member1.png', name: 'Darell B.', online: true, group: 1 },
  { id: 2, type: 'member', img: 'image/back/member2.png', name: 'Jéssica S.', online: false, group: 1 },
  { id: 3, type: 'member', img: 'image/back/member1.png', name: '2Darell B.', online: true, group: 2 },
  { id: 4, type: 'member', img: 'image/back/member2.png', name: '2Jéssica S.', online: false, group: 2 },
  { id: 5, type: 'member', img: 'image/back/member1.png', name: '3Darell B.', online: true, group: 3 },
  { id: 6, type: 'member', img: 'image/back/member2.png', name: '4Jéssica S.', online: false, group: 4 },
];
const groups = [
  { id: 1, type: 'group', img: 'image/back/group.png', name: 'Grupo 01', members: 'Darell, Jéssica', num: 1 },
  { id: 2, type: 'group', img: 'image/back/group.png', name: 'Grupo 02', members: 'Darell, Jéssica', num: 2 },
  { id: 3, type: 'group', img: 'image/back/group.png', name: 'Grupo 03', members: 'Darell, Jéssica', num: 3 },
];

const user = { img: 'image/back/avatar.png', name: 'Letícia M.' }

const ExportMsg = (props) => {

  return (
    <div className={styles.export_card}>
      <div className={styles.card_container}>
        <div className={styles.content}>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit.
        </div>
        <div className={styles.ok_btn} onClick={() => props.hideMag()}>
          Ok
        </div>
        <div className={styles.msg_close_btn} onClick={() => props.hideMag()}>
          <CloseIcon />
        </div>
      </div>
    </div>
  )
}

// let monthChartData = [];

const Report = (props) => {

  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [windowWidth, windowHeight] = useWindowSize();
  const [containerWidth, setContainerWidth] = useState(0);
  const [notification, setNotification] = useState(111);
  const [showExportMsg, setShowExportMsg] = useState(false);
  const [chartData, setChartData] = useState([]);
  const [activeCertifications, setActiveCertification] = useState([]);
  const [activeMedals, setActiveMedal] = useState([]);

  const [chartOption, setChartOption] = useState(1);
  const [chartwidth, setChartWidth] = useState(0);
  const contentRef = useRef(null);



  const divHeight = windowHeight - 262;
  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width}px)`);
  }

  useEffect(() => {
    const getData = async () => {
      dispatch(startAction())
      try {
        const resChartData = await agent.common.getChartData(auth.currentUser.id, chartOption)
        const resActCertification = await agent.common.getActiveCertification(auth.currentUser.id, chartOption)
        const resActMedal = await agent.common.getActiveMedal(auth.currentUser.id, chartOption)
        if (resChartData.data.success) {
          let newArray = []
          resChartData.data.data.map((item, idx) =>
            newArray.push({ label: item.xLabel, x: idx, y: item.Value })
          )
          setChartData([...newArray])
        }
        if (resActCertification.data.success) setActiveCertification([...resActCertification.data.data])
        if (resActMedal.data.success) setActiveMedal([...resActMedal.data.data])
        dispatch(endAction())
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()
  }, [chartOption])

  const handleChange = (num) => {
    setChartOption(Number(num))
  }

  const hideMag = () => {
    setShowExportMsg(false);
  }

  const getChartWidth = () => {
    setChartWidth(contentRef.current?.offsetWidth)
  }

  return (
    <div className={styles.main}>
      <div className={styles.report}>
        <Sidemenu getWidth={getWidth} notification={notification} active={7} />
        <div className={styles.report_container} style={{ width: containerWidth }} ref={contentRef} onLoad={() => getChartWidth()} onChange={() => getChartWidth()}>
          <div className={styles.rel_container}>
            <div className={styles.page_header}>
              <div className={styles.title}>
                Atividades
              </div>
              <div className={styles.export_pdf_btn} onClick={() => setShowExportMsg(true)}>
                Exportar PDF
              </div>
            </div>
            <div className={styles.chart_select}>
              <div className={styles.select_container}>
                <select className={styles.date_range_select} onChange={(e) => handleChange(e.target.value)}>
                  <option value={1}>Última Semana</option>
                  <option value={2}>mês passado</option>
                  <option value={3}>ano passado</option>
                </select>
                <div className={styles.select_icon}>
                  <KeyboardArrowDownIcon className={styles.arrow_down} />
                </div>
              </div>
            </div>
            <div className={styles.chart_container}>
              <div className={styles.title}>
                Tempo Online
              </div>
              <div className={styles.report_chart}>
                {
                  chartData.length > 0 &&
                  <LineChart
                    width={chartwidth}
                    height={220}
                    data={chartData}
                    horizontalGuides={5}
                    precision={0}
                    verticalGuides={0}
                  />
                }
              </div>
            </div>
            <div className={styles.certification_container}>
              <div className={styles.title}>
                Certificados
              </div>
              <div className={styles.certification_content}>
                {
                  activeCertifications.map((activeCertification, idx) =>
                    <ReportCertificationAcc value={activeCertification} content={<CertificationAccItem value={activeCertification.contentData} key={idx} />} />
                  )
                }
              </div>
            </div>
            <div className={styles.medal_container}>
              <div className={styles.title}>
                Medalhas
              </div>
              <div className={styles.medal_content}>
                {
                  activeMedals.map((activeMedal, idx) => <AchievementAcc value={activeMedal} content={<AchievementItem value={activeMedal.content} />} key={idx} />)
                }
              </div>
            </div>
            {
              showExportMsg && <ExportMsg hideMag={hideMag} />
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default Report