import React, { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import CheckIcon from '@mui/icons-material/Check';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleLeft } from '@fortawesome/fontawesome-free-solid'

import styles from './Signup.module.scss';
import { useWindowSize } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'
import { logout } from "../../actions/auth";

let intervalID = 0;
let callCount = 0;

const SuccessMessage = () => {
  return (
    <div className={styles.message}>
      <div className={styles.check_container}>
        <div className={styles.check_icon}>
          <CheckIcon />
        </div>
      </div>
      <div className={styles.title_container}>
        <div className={styles.title}>
          Sua nova senha foi alterada com sucesso!
        </div>
      </div>
      <div className={styles.content_container}>
        A confirmação enviada para o seu e-mail.
      </div>
    </div>
  )
}

const Signup = () => {

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const auth = useSelector(state => state.auth)

  const [windowWidth, windowHeight] = useWindowSize();
  const [show, setShow] = useState(false);
  const [password, setPassword] = useState('');
  const [conPassword, setConPassword] = useState('');
  const [pswError, setPswError] = useState(false);
  const [conpswError, setConpswError] = useState(false);


  const showSuccessMsg = async () => {
    if (checkValidation()) {
      dispatch(startAction())
      try {
        const res = await agent.auth.updatePassword(auth.currentUser.id, password)
        if (res.data.success) {
          dispatch(showToast('success', res.data.message))
          dispatch(changePage('login'))
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
      setShow(true)
      intervalID = setInterval(function () {
        dispatch(changePage('login'))
        return clearInterval(intervalID);
      }, 3000);
    }
  }

  useEffect(() => {
    if (callCount > 0) checkValidation()
    callCount++;
  }, [password, conPassword])

  const checkValidation = () => {
    if (password !== '' && conPassword !== '' && password === conPassword) {
      setPswError(false)
      setConpswError(false)
      return true
    }

    if (password === '') {
      setPswError(true)
    }

    if (conPassword === '') {
      setConpswError(true)
    }

    if (password !== conPassword) {
      setPswError(true)
      setConpswError(true)
    }
    return false
  }

  return (
    <div className={styles.main}>
      <div className={styles.signup}>
        {
          show && <SuccessMessage />
        }
        {!show &&
          <div className={styles.signup_card}>
            <div className={styles.card_title}>
              Obrigado por acessar o Universo Líder KOF
            </div>
            <div className={styles.card_subtitle}>
              Crie uma senha exclusiva para acesso do portal.
            </div>
            <div className={`${styles.signup_input_container} ${styles.password}`}>
              <div className={styles.signup_input_label}>
                Nova Senha
              </div>
              <input id="password" onChange={(e) => setPassword(e.target.value)} className={`${styles.signup_input} ${(pswError ? styles.error : '')}`} placeholder="Nova Senha" type="password" />
            </div>
            <div className={`${styles.signup_input_container} ${styles.repassword}`}>
              <div className={styles.signup_input_label}>
                Confirmar nova Senha
              </div>
              <input id="re-password" onChange={(e) => setConPassword(e.target.value)} className={`${styles.signup_input} ${(conpswError ? styles.error : '')}`} placeholder="Confirmar nova Senha" type="password" />
            </div>
            <div className={styles.signup_confirm_btn_container}>
              <div className={styles.prev_btn} onClick={() => dispatch(changePage('login'))}>
                <FontAwesomeIcon icon={faAngleLeft} />
              </div>
              <button className={styles.signup_confirm_btn} onClick={() => showSuccessMsg()}>Confirmar</button>
            </div>
          </div>}
      </div>
    </div>
  )
}

export default Signup