import React from "react";

import styles from './Member.module.scss';
import { getImg } from "../../../utils/Helper";
const Member = (props) => {

	return (
		<div className={styles.member} onClick={() => props.memberClick(props.value.id)}>
			<div className={styles.member_container}>
				<div className={styles.member_avatar}>
					<img className={styles.avatar} src={getImg(`${props.value.img}`)} alt="Logo" />
				</div>
				<div className={styles.member_info_container}>
					<div className={styles.member_name}>
						{props.value.name}
					</div>
					<div className={styles.online_status}>
						<div className={(props.value.online ? styles.online : styles.offline)}>
							<span>
								{props.value.online ? 'Online' : 'Offline'}
							</span>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default Member