import React, { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Link, useNavigate } from "react-router-dom";

import { getImg, useWindowSize } from "../../utils/Helper";

import Login from '../Login';
import Signup from '../Signup';
import Home from '../Home';
import Play from '../Play';
import Profile from '../Profile';
import EditProfile from '../EditProfile';
import EditInfo from '../EditInfo';
import University from '../University';
import UniversityLayout from '../UniversityLayout';
import UniversityDetail from '../UniversityDetail';
import UniversityContent from '../UniversityContent';
import UniversityInstructor from '../UniversityInstructor';
import InstructorInfo from '../InstructorInfo';
import Schedule from '../Schedule';
import Ranking from '../Ranking';
import Progress from '../Progress';
import Report from '../Report';
import NonePage from '../NonePage';

import ProtectedRoute from '../../utils/ProtectedRoute'
import NonProtectedRoute from '../../utils/NonProtectedRoute'

import {
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from "../../actions/auth"


const Main = (props) => {
  const common = useSelector(state => state.common)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const contentRef = useRef(null);

  return (
    <>
      {common.page === 'login' && <NonProtectedRoute><Login /></NonProtectedRoute>}
      {common.page === 'signup' && <NonProtectedRoute><Signup /></NonProtectedRoute>}
      {common.page === 'home' && <ProtectedRoute><Home /></ProtectedRoute>}
      {common.page === 'play' && <ProtectedRoute><Play /></ProtectedRoute>}
      {common.page === 'ranking' && <ProtectedRoute><Ranking /></ProtectedRoute>}
      {common.page === 'report' && <ProtectedRoute><Report /></ProtectedRoute>}
      {common.page === 'progress' && <ProtectedRoute><UniversityLayout active={3} component={<Progress />} /></ProtectedRoute>}
      {common.page === 'university' && <ProtectedRoute><UniversityLayout active={2} component={<University />} /></ProtectedRoute>}
      {common.page === 'instructor' && <ProtectedRoute><UniversityLayout active={2} component={<UniversityInstructor />} /></ProtectedRoute>}
      {common.page === 'instructorinfo' && <ProtectedRoute><UniversityLayout active={2} component={<InstructorInfo id={common.pageId} />} /></ProtectedRoute>}
      {common.page === 'detail' && <ProtectedRoute><UniversityLayout active={2} component={<UniversityDetail id={common.pageId} />} /></ProtectedRoute>}
      {common.page === 'content' && <ProtectedRoute><UniversityLayout active={2} component={<UniversityContent id={common.pageId} />} /></ProtectedRoute>}
      {common.page === 'schedule' && <ProtectedRoute><Schedule /></ProtectedRoute>}
      {common.page === 'profile' && <ProtectedRoute><Profile /></ProtectedRoute>}
      {common.page === 'editinfo' && <ProtectedRoute><EditInfo /></ProtectedRoute>}
      {common.page === 'editprofile' && <ProtectedRoute><EditProfile /></ProtectedRoute>}
      {common.page === 'none' && <ProtectedRoute><NonePage /></ProtectedRoute>}
    </>
  )
}

export default Main