import React, { useState, useEffect } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import Sidemenu from "../../components/Sidemenu";

import styles from './Home.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";
import { bytesToBase64, base64ToBytes } from "../../utils/base64";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'
import { logout } from "../../actions/auth";

const content_text = `Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting.`;

const Home = (props) => {

  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [windowWidth, windowHeight] = useWindowSize();
  const [containerWidth, setContainerWidth] = useState('');
  const [campus, setCampus] = useState({});
  const [image, setImage] = useState('');

  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width}px)`);
  }

  const handleClickPlay = () => {
    dispatch(changePage('play'))
    window.dragon.makeCharacter(auth.characterData)
    window.dragon.clickPlay(auth.currentUser.role)
  }

  useEffect(() => {
    const getData = async () => {
      try {
        const res = await agent.common.getCampus()
        if (res.data.success) {
          setCampus({ ...res.data.data })
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()
  }, [])

  useEffect(() => {
    console.log("call home useEffect")
    if (auth.characterData.gendor !== -1) {
      console.log("character data=", auth.characterData)
      let modelData = auth.characterData
      modelData.id = auth.currentUser.id
      const imageData = localStorage.getItem('avatar1_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin)
      window.dragon.makeCharacter(modelData)
      if (imageData == undefined || imageData == null) {
        window.dragon.sendCharacterData(0)
      } else {
        setImage(imageData)
      }
    }
  }, [auth.characterData])

  window.dragon.saveImage1 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar1_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
    setImage(encodeURIComponent(data))
  }

  window.dragon.saveImage2 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar2_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  window.dragon.saveImage3 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar3_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  window.dragon.saveImage4 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar4_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  return (
    <div className={styles.main}>
      <div className={styles.home}>
        <Sidemenu getWidth={getWidth} active={0} />
        <div className={styles.home_container} style={{ width: containerWidth }}>
          <div className={styles.home_card_container}>
            <div className={styles.home_card}>
              <div className={styles.home_card_img_container}>
                {
                  image === '' ?
                    <img className={styles.home_card_img} src={getImg('image/back/home_big.png')} alt="Logo" />
                    :
                    <img className={styles.home_card_img} src={"data:image/png;base64," + image} alt="Logo" />
                }
              </div>
              <div className={styles.home_card_title}>
                Bem-Vindo!
              </div>
              <div className={styles.home_card_content_container}>
                <div className={styles.first_content}>
                  <div className={styles.home_card_content_img_container}>
                    <img className={styles.sidemenu_header_effect_img} src={getImg('image/back/home_logo.jpg')} alt="Logo" />
                  </div>
                  <div className={styles.home_card_content_title}>
                    Universo Líder KOF
                  </div>
                </div>
                <div className={styles.second_content}>
                  <img className={styles.sidemenu_header_effect_img} src={getImg('image/back/home_logo_2.png')} alt="Logo" />
                </div>
                <div className={styles.second_content_btn_container}>
                  <button className={styles.second_content_btn} onClick={() => handleClickPlay()}>Acessar Universo</button>
                </div>
              </div>
            </div>
            <div className={styles.home_chat_img_container}>
              <img className={styles.home_chat_img} src={getImg('image/icons/chat_btn.png')} alt="Logo" />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home