import React, { useEffect, useState, useRef } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';

import Notification from '../../components/Sidemenu/Notification'

import styles from './Play.module.scss'
import { getImg, useWindowSize } from "../../utils/Helper"


import agent from '../../api/'

import {
  changePage,
  startAction,
  endAction,
  showToast,
  isLive
} from '../../actions/common'
import { logout, createCharacter } from '../../actions/auth'



const Play = (props) => {

  const navigate = useNavigate()
  const dispatch = useDispatch()
  const auth = useSelector(state => state.auth)
  const common = useSelector(state => state.common)

  const [windowWidth, windowHeight] = useWindowSize()

  const [mapData, setMapData] = useState([])
  const [showNotification, setShowNotification] = useState(false)
  const [notification, setNotification] = useState(0)
  const [notificationData, setNotificationData] = useState([])
  const [showBtn, setShowBtn] = useState(false)
  const [showFullScreenBtn, setShowFullScreenBtn] = useState(false)
  const [lockCamera, setLockCamera] = useState(true)
  const [avatarImg, setAvatarImg] = useState('');

  const handleClick = async () => {
    window.dragon.clickMap(mapData)
    window.dragon.unityKeyboard()
  }

  const toggleNotification = () => {
    setShowNotification((old) => old ? false : true)
  }

  const getCustomizeData = async () => {
    try {
      const res = await agent.common.getCustomizeModel(auth.currentUser.id)
      if (res.data.success) {
        dispatch(changePage('none'))
        dispatch(createCharacter(res.data))
        window.dragon.clickCustomize(res.data.data)
      }
    } catch (error) {
      if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
        dispatch(showToast('error', error.response.data.message))
        if (error.response.data.message == 'Unauthorized') {
          localStorage.removeItem('token')
          dispatch(logout())
          window.dragon.stopUnityGame()
          dispatch(changePage('login'))
        }
      }
    }
  }

  // const getNotificationData = async () => {
  //   try {
  //     const resNotificationtData = await agent.common.getNotification()
  //     if (resNotificationtData.data.success) {
  //       setNotificationData([...resNotificationtData.data.data])
  //       let count = 0;
  //       resNotificationtData.data.data.map((alarm) => {
  //         if (!alarm.read) {
  //           count++;
  //         }
  //       })
  //       setNotification(count)
  //     }
  //   } catch (error) {
  //     if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
  //       dispatch(showToast('error', error.response.data.message))
  //       if (error.response.data.message == 'Unauthorized') {
  //         localStorage.removeItem('token')
  //         dispatch(logout())
  //         window.dragon.stopUnityGame()
  //         dispatch(changePage('login'))
  //       }
  //     }
  //   }
  // }

  const handleClickNotification = () => {
    // getNotificationData()
    toggleNotification()
  }

  const clickFullScreen = (full) => {
    setShowFullScreenBtn(full)

    var videoGroup = document.getElementById('dragon-live-video')
    var fullVideo = document.getElementsByClassName('dragon-class')

    console.log("fullvideo length=", fullVideo.length)

    if (fullVideo.length == 0) return

    let count = fullVideo.length
    for (let j = 0; j < fullVideo.length; j++) {
      // if (fullVideo[j].parentElement.id == "local-player")
      //   count++
      switch (count) {
        case 1:
          fullVideo[j].style.width = "100%"
          videoGroup.style.display = 'block'
          videoGroup.src = './assets/screens/Coca-cola1.png'
          break
        case 2:
          fullVideo[j].style.width = "50%"
          videoGroup.style.display = 'block'
          videoGroup.src = './assets/screens/Coca-cola2.png'
          break
        case 3:
          fullVideo[j].style.width = "33.3333%"
          videoGroup.style.display = 'none'
          break
        case 4:
          fullVideo[j].style.width = "50%"
          fullVideo[j].style.height = "50%"
          videoGroup.style.display = 'none'
          break
        case 5:
        case 6:
          fullVideo[j].style.width = "33.3333%"
          fullVideo[j].style.height = "50%"
          videoGroup.style.display = 'block'
          videoGroup.src = './assets/screens/Coca-cola6.png'
          break
      }
    }

    if (count == 0) {
      document.getElementById("dragon-video-group").style.height = "0px"
    } else {
      document.getElementById("dragon-video-group").style.height = "100vh"
    }

    if (full) {
      console.log("show full video")
      document.getElementById("unity-canvas").style.display = "none"
      for (let i = 0; i < fullVideo.length; i++) {
        fullVideo[i].style.display = 'block'
      }
    } else {
      console.log("hide full video")
      document.getElementById("unity-canvas").style.display = "block"
      videoGroup.style.display = 'none'
      for (let k = 0; k < fullVideo.length; k++) {
        fullVideo[k].style.display = 'none'
      }
    }
  }

  const exitClick = () => {
    document.getElementById("unity-canvas").style.display = "block"
    var videoGroup = document.getElementById('dragon-live-video')
    videoGroup.style.display = 'none'
    var fullVideo = document.getElementsByClassName('dragon-class')
    for (let k = 0; k < fullVideo.length; k++) {
      fullVideo[k].style.display = 'none'
    }
    window.dragon.outLectureRoom()
  }

  const lockClick = (isLock) => {
    setLockCamera(isLock)
    if (isLock) {
      window.dragon.lockCamera(1)
    } else {
      window.dragon.lockCamera(0)
    }
  }

  const goOutPlay = (page) => {
    dispatch(changePage(page))
    window.dragon.clickHome()
    window.dragon.outLectureRoom()
    window.dragon.reactKeyboard()
    setShowBtn(false)
  }

  useEffect(() => {
    const getData = async () => {
      try {
        const res = await agent.common.getMapData(auth.currentUser.id)
        if (res.data.success) {
          setMapData([...res.data.data])
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()

    let modelData = auth.characterData
    setAvatarImg(localStorage.getItem('avatar3_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin))

    if (common.isLive) {
      setLockCamera(true)
      window.dragon.makeCharacter(auth.characterData)
      window.dragon.clickPlay(auth.currentUser.role)
      window.dragon.adminLecture(auth.currentUser.role)
      dispatch(isLive(false))
    }
  }, [])

  useEffect(() => {
    const getUserData = async () => {
      try {
        const resNotificationData = await agent.common.getNotification()
        if (resNotificationData.data.success) {
          let notificationArray = []
          resNotificationData.data.data.map((notUser) => {
            notificationArray.push({
              id: notUser.id,
              title: notUser.title,
              description: notUser.message,
              type: notUser.type,
              link: "",
              viewed: notUser.read,
            })
          })

          setNotificationData([...notificationArray])
          let count = 0;
          notificationArray.map((alarm) => {
            if (!alarm.viewed) {
              count++;
            }
          })
          setNotification(count)
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
          // clearInterval(intervalID);
          // console.log("interval id: ", intervalID)
          // intervalID = -1
        }
      }
    }

    const getAdminData = async () => {
      try {
        const resNotificationData = await agent.common.notification()
        const resNotificationUserData = await agent.common.notificationUser()
        if (resNotificationData.data.success && resNotificationUserData.data.success) {
          let notificationArray = []
          resNotificationUserData.data.data.map((notUser) => {
            if (notUser.id_user === auth.currentUser.id) {
              for (let i = 0; i < resNotificationData.data.data.length; i++) {
                if (notUser.id_notification === resNotificationData.data.data[i].id) {
                  let type
                  switch (resNotificationData.data.data[i].type) {
                    case "LESSON":
                      type = 1
                      break
                    case "MEETING":
                      type = 2
                      break
                    case "INTERACTION":
                      type = 3
                      break
                  }
                  notificationArray.push({
                    id: notUser.id_notification,
                    title: resNotificationData.data.data[i].title,
                    description: resNotificationData.data.data[i].description,
                    type: type,
                    link: resNotificationData.data.data[i].link,
                    viewed: notUser.viewed,
                  })
                }
              }
            }
          })

          setNotificationData([...notificationArray])
          let count = 0;
          notificationArray.map((alarm) => {
            if (!alarm.viewed) {
              count++;
            }
          })
          setNotification(count)
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
          // clearInterval(intervalID);
          // console.log("interval id: ", intervalID)
          // intervalID = -1
        }
      }
    }

    if (auth.currentUser.role === 'ADMIN') {
      getAdminData()
    } else {
      getUserData()
    }

    // intervalID = setInterval(function () {
    // 	getData()
    // 	clearInterval(intervalID);
    // }, 10000);
  }, [])



  window.dragon.saveFavorite = function (data) {
    const setData = async (data) => {
      const res = await agent.common.setMapData(
        auth.currentUser.id,
        data
      )
      if (res.data.success) dispatch(showToast('success', res.data.message))
      else dispatch(showToast('error', res.data.message))
    }
    console.log("setData", data)
    setData(data)
  }

  window.dragon.showMoreBtn = function (isShow) {
    setShowBtn(isShow)
  }

  return (
    <div className={styles.main}>
      <div className={styles.home}>
        <div className={styles.world}>
          <div className={styles.left_border_img_container}>
            <img className={styles.left_border_img} src={getImg('image/back/small_sidemenu_back.png')} alt="Logo" />
          </div>
          <div className={styles.world_logo_control_container}>
            <div className={styles.world_logo_container} onClick={() => getCustomizeData()}>
              {
                avatarImg === '' ?
                  <img className={styles.left_border_img} src={getImg('image/back/avatar.png')} alt="Logo" />
                  :
                  <img className={styles.left_border_img} src={"data:image/png;base64," + avatarImg} alt="Logo" />
              }
            </div>
            <div className={styles.world_logo_text} onClick={() => goOutPlay('home')}>
              Voltar
            </div>
          </div>
          <div className={styles.world_header_btn_container}>
            <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => goOutPlay('progress')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn1.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_medium_btn}`} onClick={() => goOutPlay('ranking')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn2.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_large_btn}`} onClick={() => handleClick()}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn3.png')} alt="icons" />
            </div>
            <div className={`${styles.button} ${styles.world_header_medium_btn}`} onClick={() => goOutPlay('schedule')}>
              <img className={styles.world_btn} src={getImg('image/icons/playbtn4.png')} alt="icons" />
            </div>
            <div className={`${styles.world_header_small_btn} ${styles.notification_btn}`}>
              <div className={`${styles.click_btn} ${styles.button}`} onClick={() => handleClickNotification()}>
                <Badge className={styles.badge} badgeContent={notification}>
                  <img className={styles.world_btn} src={getImg('image/icons/playbtn5.png')} alt="icons" />
                </Badge>
              </div>
              <div className={styles.notification_container}>
                <div className={styles.rel_container}>
                  {
                    showNotification && <Notification alarms={notificationData} toggleNotification={toggleNotification} />
                  }
                </div>
              </div>
            </div>

          </div>

          {
            showBtn &&
            <div className={styles.world_footer_btn_container}>
              <div className={`${styles.button} ${styles.world_header_small_btn}  ${styles.lock}`} onClick={() => exitClick()}>
                <div className={styles.btn_text}>Exit</div>
              </div>
              {
                lockCamera ?
                  <div className={`${styles.button} ${styles.world_header_small_btn} ${styles.lock}`} onClick={() => lockClick(false)}>
                    <div className={styles.btn_text}>Unlock</div>
                  </div>
                  :
                  <div className={`${styles.button} ${styles.world_header_small_btn} ${styles.lock}`} onClick={() => lockClick(true)}>
                    <div className={styles.btn_text}>lock</div>
                  </div>
              }
              {
                !showFullScreenBtn ?
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => clickFullScreen(true)}>
                    <div className={styles.btn_text}>+</div>
                  </div>
                  :
                  <div className={`${styles.button} ${styles.world_header_small_btn}`} onClick={() => clickFullScreen(false)}>
                    <div className={styles.btn_text}>-</div>
                  </div>
              }
            </div>
          }
        </div>
      </div>
    </div>
  )
}

export default Play