import React, { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Link, useParams, useNavigate } from 'react-router-dom';

import Grid from '@mui/material/Grid';
import CheckIcon from '@mui/icons-material/Check';

import Video from "../../components/Video";
import Accordion from "../../components/Accordion";
import VideoDetailText from "../../components/AccordionItem/VideoDetailText";
import Forumitem from "../../components/University/Forumitem";
import Pagination from "../../components/University/Pagination";

import styles from './UniversityDetail.module.scss';
import { getImg } from "../../utils/Helper";

import {
	changePage,
	startAction,
	endAction,
	showToast,
	changePageId
} from '../../actions/common'
import { logout } from "../../actions/auth";
import agent from '../../api/'

const UniversityDetail = (props) => {
	const auth = useSelector(state => state.auth)
	const dispatch = useDispatch()
	const navigate = useNavigate()
	let { id } = useParams()

	const [forumShow, setForumShow] = useState(3);
	const [forumAllShow, setForumAllShow] = useState(false);
	const [descriptionShow, setDescriptionShow] = useState(false);
	const [paginationShowCount, setPaginationShowCount] = useState(0);
	const [courseDetail, setCourseDetail] = useState({});
	const [whatLearns, setWhatLearn] = useState([]);
	const [classes, setClasses] = useState([]);
	const [relativeCourses, setRelativeCourse] = useState([]);
	const [forums, setForum] = useState([]);
	const [isPause, setIsPause] = useState(false);
	const [videoTime, setVideoTime] = useState(0);
	const contentRef = useRef(null);

	const moreView = () => {
		setForumShow((oldForumShow) => oldForumShow + 3 >= forums.length ? forums.length : oldForumShow + 3)
		if (forumShow + 3 >= forums.length) {
			setForumAllShow(true);
		}
	}

	const getCount = () => {
		const contentWidth = contentRef.current?.offsetWidth;
		const count = Math.floor(contentWidth / 230);
		setPaginationShowCount(count);
	}

	const updateStatus = async (idx, sta) => {
		dispatch(startAction())
		const res = await agent.common.updateEnjoy(auth.currentUser.id, forums[idx].id, sta)
		if (res.data.success) dispatch(showToast('success', res.data.message))
		else dispatch(showToast('error', res.data.message))
		dispatch(endAction())
		let newArray = forums;
		newArray[idx].status = sta
		setForum([...newArray])
	}

	const goPage = (page, id) => {
		dispatch(changePage(page))
		dispatch(changePageId(id))
	}

	const clickWatchLater = async () => {
		dispatch(startAction())
		try {
			const resAddWatchLater = await agent.common.addWatchLater(Number(props.id))
			if (resAddWatchLater.data.success) dispatch(showToast('success', resAddWatchLater.data.message))
			dispatch(endAction())
		} catch (error) {
			if (error.response.status >= 400 && error.response.status <= 415) {
				dispatch(endAction())
				dispatch(showToast('Failed', error.response.data.message))
				if (error.response.data.message == 'Unauthorized') {
					localStorage.removeItem('token')
					dispatch(logout())
					window.dragon.stopUnityGame()
					dispatch(changePage('login'))
				}
			}
		}
	}

	useEffect(() => {
		const getData = async () => {
			dispatch(startAction())
			try {
				const resCourseDetail = await agent.common.getCourseDetail(auth.currentUser.id, Number(props.id))
				const resWhatLearn = await agent.common.getWhatLearn(Number(props.id))
				const resClasses = await agent.common.getClasses(Number(props.id))
				const resRelativeCourse = await agent.common.getRelativeCourse(Number(props.id))
				// const resForum = await agent.common.getForumByCourse(Number(props.id))

				if (resCourseDetail.data.success) setCourseDetail({ ...resCourseDetail.data.data })
				if (resWhatLearn.data.success) setWhatLearn([...resWhatLearn.data.data])
				if (resClasses.data.success) setClasses([...resClasses.data.data])
				if (resRelativeCourse.data.success) setRelativeCourse([...resRelativeCourse.data.data])
				// if (resForum.data.success) setForum([...resForum.data.data])
				dispatch(endAction())
			} catch (error) {
				if (error.response.status >= 400 && error.response.status <= 415) {
					dispatch(endAction())
					dispatch(showToast('Failed', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
				}
			}
		}
		getData()
	}, [props.id])

	return (
		<div className={styles.university_content}>
			<div className={styles.tab_btn_container}>
				<div className={styles.tab_btn}>
					<button className={`${styles.tab_first_btn} ${styles.w_back}`}>Conteúdos</button>
					<button className={`${styles.tab_second_btn} ${styles.r_back}`} onClick={() => dispatch(changePage('instructor'))}>Instrutores</button>
				</div>
			</div>
			<div className={styles.university_detail}>
				<div className={styles.detail_info} ref={contentRef} onLoad={() => getCount()}>
					<div className={styles.content_title}>
						<div className={styles.content}>
							Conteúdos
						</div>
						<div className={styles.title}>
							<div onClick={() => dispatch(changePage('university'))}>
								Voltar
							</div>
						</div>
					</div>
					<div className={styles.info}>
						<div className={styles.left_info}>
							<div className={styles.left_header}>
								<div className={styles.info_title}>
									{courseDetail.title}
								</div>
								<div className={styles.info_subtitle}>
									{courseDetail.subtitle}
								</div>
							</div>
							<div className={styles.description}>
								<div className={styles.description_title}>
									Descrição
								</div>
								<div className={(descriptionShow ? styles.description_all_content : styles.description_content)}>
									{courseDetail.description}

								</div>
							</div>
							<div className={styles.more_view}>
								<button onClick={() => setDescriptionShow((old) => !old)}>Mostrar mais</button>
							</div>
							<div className={styles.more_info}>
								<div className={styles.left_more_info}>
									<div className={styles.more_info_img}>
										<img src={getImg('image/icons/users.png')} alt="icon" />
									</div>
									<div className={styles.more_info_content}>
										<div className={styles.more_info_content_title}>
											Total de Alunos
										</div>
										<div className={styles.content}>
											{courseDetail.studentsCount}
										</div>
									</div>
								</div>
								<div className={styles.right_more_info}>
									<div className={styles.more_info_img}>
										<img src={getImg('image/icons/total_video.png')} alt="icon" />
									</div>
									<div className={styles.more_info_content}>
										<div className={styles.more_info_content_title}>
											Total de Aulas
										</div>
										<div className={styles.content}>
											{courseDetail.count}
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className={styles.right_info}>
							<div className={styles.video_container}>
								<Video id={courseDetail.videoId} setIsPause={setIsPause} setVideoTime={setVideoTime} value={{ video: courseDetail.video, lastTime: courseDetail.lastTime }} height={227} />
							</div>
							<div className={styles.video_info}>
								{courseDetail.count} Aulas&nbsp;&nbsp;·&nbsp;&nbsp;{courseDetail.time} horas no total&nbsp;&nbsp;·&nbsp;&nbsp;{courseDetail.text}
							</div>
							<div className={styles.now_btn}>
								<button onClick={() => goPage('content', props.id)}>Assistir Agora</button>
							</div>
							<div className={styles.later_btn}>
								<button onClick={() => clickWatchLater()} >Assistir mais tarde</button>
							</div>
						</div>
					</div>
				</div>
				{
					whatLearns.length !== 0 &&
					<div className={styles.want}>
						<div className={styles.want_title}>
							O que você aprenderá
						</div>
						<Grid container spacing={2} style={{ marginLeft: -40, width: 'calc(100% + 40px)' }}>
							{whatLearns.map((item, idx) =>
								<Grid item md={6} key={idx} style={{ paddingLeft: 40 }}>
									<div className={styles.want_item}>
										<div className={styles.check_icon}>
											<CheckIcon />
										</div>
										<div className={styles.want_content}>
											{item.text}
										</div>
									</div>
								</Grid>
							)}
						</Grid>
					</div>
				}

				<div className={styles.video_list}>
					{classes.map((item, idx) =>
						<Accordion value={item} content={<VideoDetailText value={item.classdata} key={idx} />} />
					)}
				</div>

				{/* <div className={styles.forum}>
					<div className={styles.forum_title}>
						Fórum
					</div>
					<div className={styles.forum_container}>
						{forums.map((forum, idx) => {
							if (idx < forumShow) {
								if (idx === forumShow - 1 && !forumAllShow) {
									return <Forumitem value={forum} lastview={true} key={idx} index={idx} updateStatus={updateStatus} />
								} else {
									return <Forumitem value={forum} lastview={false} key={idx} index={idx} updateStatus={updateStatus} />
								}
							}
						})}
						{
							!forumAllShow &&
							<button onClick={() => moreView()} className={styles.more_view_btn}>Mostrar mais</button>
						}
					</div>
				</div> */}
				{
					relativeCourses.length > 0 &&
					<div className={styles.related_course}>
						<div className={styles.title}>
							Cursos relacionados
						</div>
						<div className={styles.content} >
							<Pagination items={relativeCourses} count={paginationShowCount} rows={['']} type={1} />
						</div>
					</div>
				}
			</div >
		</div>

	)
}

export default UniversityDetail