import React, { useEffect, useRef } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate } from 'react-router'
import { useNavigate } from 'react-router-dom'

import agent from '../api/'

import { login, logout } from '../actions/auth'
import { changePage, showToast, showChat } from '../actions/common'

const ProtectedRoute = props => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const token = localStorage.getItem('token')
  const auth = useSelector(state => state.auth)
  const { loadLogin } = useSelector(state => state.common)
  let intervalID = useRef(null)


  useEffect(() => {
    // if (!loadLogin) {
    //   console.log("loadLogin false")
    //   dispatch(changePage('login'))
    // }
    async function getAuthInfo() {
      if (!auth.currentUser.email) {
        try {
          const res = await agent.auth.me()
          if (res.data.success) dispatch(login(res.data))
        } catch (error) {
          if (error === 'Error: Network Error') {
            window.location.reload();
          }
          if (error.response != undefined && error.response.status >= 400 && error.response.status <= 415) {
            localStorage.removeItem('token')
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getAuthInfo()
    dispatch(showChat(true))
  }, [])

  // useEffect(() => {
  //   async function setNewToken() {
  //     try {
  //       const res = await agent.auth.renewToken()
  //       if (res.data.success) {
  //         localStorage.setItem('token', res.data.token)
  //       }
  //     } catch (error) {
  //       if (error.response.status >= 400 && error.response.status <= 415) {
  //         clearInterval(intervalID.current);
  //         dispatch(showToast('error', error.response.data.message))
  //         if (error.response.data.message == 'Unauthorized') {
  //           localStorage.removeItem('token')
  //           dispatch(logout())
  //           window.dragon.stopUnityGame()
  //           dispatch(changePage('login'))
  //         }
  //       }
  //     }
  //   }

  //   intervalID.current = setInterval(function () {
  //     if (localStorage.getItem('token') !== undefined && localStorage.getItem('token') !== null) setNewToken()
  //   }, 60000);
  // }, [])

  if (auth.currentUser.email) {
    return <React.Fragment>{props.children}</React.Fragment>
  }
  if (!token) {
    return dispatch(changePage('login'))
  }
  return <></>
}

export default ProtectedRoute