import React, { useEffect, useState } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import CloseIcon from '@mui/icons-material/Close'

import NotificationItem from "./NotificationItem"
import styles from './Notification.module.scss'
import { getImg } from "../../../utils/Helper"

import {
	startAction,
	endAction,
	showToast
} from '../../../actions/common'
import { logout } from "../../../actions/auth"
import agent from '../../../api'

const Notification = (props) => {

	const auth = useSelector(state => state.auth)
	const navigate = useNavigate()
	const dispatch = useDispatch()

	const [tab, setTab] = useState(1);
	const [tabCount, setTabCount] = useState([0, 0, 0]);

	const tabClick = (num) => {
		setTab(num)
	}

	const markRead = async () => {

		dispatch(startAction())
		const res = await agent.common.setReadNotification(auth.currentUser.id, tab)
		if (res.data.success) dispatch(showToast('success', res.data.message))
		else dispatch(showToast('error', res.data.message))
		dispatch(endAction())
		// let new_array = tabCount
		// new_array[tab] = 0
		// setTabCount([...new_array])
		// props.alarms.map((alarm) => {
		// 	if (alarm.type === tab && !alarm.read) {
		// 		alarm.read = true
		// 	}
		// })
		// props.setNtf(new_array[0] + new_array[1] + new_array[2])
	}

	useEffect(() => {
		let new_array = tabCount
		props.alarms.map((alarm) => {
			if (alarm.type === 1 && !alarm.viewed) {
				new_array[0]++
			} else if (alarm.type === 2 && !alarm.viewed) {
				new_array[1]++
			} else if (alarm.type === 3 && !alarm.viewed) {
				new_array[2]++
			}
			setTabCount([...new_array])
		})
	}, [])

	return (
		<div className={styles.notification}>
			<div className={styles.notification_container}>
				<div className={styles.header}>
					<div className={styles.tab_btn} onClick={() => tabClick(1)}>
						Aulas
						{tab === 1 && <div className={styles.footer_bar}></div>}
						{tabCount[0] > 0 && <div className={styles.tab_count}>{tabCount[0]}</div>}
					</div>
					<div className={styles.tab_btn} onClick={() => tabClick(2)}>
						Reuniões
						{tab === 2 && <div className={styles.footer_bar}></div>}
						{tabCount[1] > 0 && <div className={styles.tab_count}>{tabCount[1]}</div>}
					</div>
					<div className={styles.tab_btn} onClick={() => tabClick(3)}>
						Interações
						{tab === 3 && <div className={styles.footer_bar}></div>}
						{tabCount[2] > 0 && <div className={styles.tab_count}>{tabCount[2]}</div>}
					</div>
				</div>
				<div className={styles.content}>
					<div className={styles.mark_read}>
						<div className={styles.mark_read_btn} onClick={() => markRead()}>
							Marcar como lida
						</div>
					</div>
					<div className={styles.list_container}>
						{props.alarms.slice(0).reverse().map((item, idx) => {
							if (item.type === tab) {
								return <NotificationItem value={item} key={idx} />
							}
						})}
					</div>
				</div>
				<div className={styles.close_btn} onClick={() => props.toggleNotification()}>
					<CloseIcon />
				</div>
			</div>
		</div>
	)
}

export default Notification