import React, { useEffect, useRef, useState } from "react";
import moment from 'moment'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';

import styles from './CertificationAcc.module.scss';
import { getImg } from "../../utils/Helper";

function CertificationAcc(props) {
	const [isShow, setIsShow] = useState(false);

	const handleClick = () => {
		setIsShow((oldValue) => oldValue ? false : true)
	}

	return (
		<div className={styles.accordion}>
			<div className={styles.main} onClick={() => handleClick()}>
				<div className={styles.main_icon}>
					{
						isShow ?
							<KeyboardArrowUpIcon /> :
							<KeyboardArrowDownIcon />
					}
				</div>
				<div className={styles.medal_img_container}>
					<div className={styles.medal_img}>
						<img src={getImg('image/icons/certification_medal.png')} alt="medal" />
					</div>
				</div>
				<div className={styles.text}>
					<div className={styles.title}>
						{props.value.title}
					</div>
					<div className={styles.description}>
						{`Concluido em ${moment(new Date(props.value.completeDate)).format('DD/MM/YYYY')}`}
					</div>
				</div>
			</div>
			{isShow &&
				<div className={styles.content}>
					{props.content}
				</div>
			}
		</div>
	);
}

export default CertificationAcc;
