import React, { useEffect, useState, useRef } from "react";
import { useSelector, useDispatch } from 'react-redux'

import Chatlist from "../Chatlist";
import Chatmain from "../Chatmain";

import styles from './ChatBtn.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
	chatting
} from '../../actions/common'

const members = [
	{ id: 1, type: 'member', img: 'image/back/member1.png', name: 'Darell B.', online: true, group: 1 },
	{ id: 2, type: 'member', img: 'image/back/member2.png', name: 'Jéssica S.', online: false, group: 1 },
	{ id: 3, type: 'member', img: 'image/back/member1.png', name: '2Darell B.', online: true, group: 2 },
	{ id: 4, type: 'member', img: 'image/back/member2.png', name: '2Jéssica S.', online: false, group: 2 },
	{ id: 5, type: 'member', img: 'image/back/member1.png', name: '3Darell B.', online: true, group: 3 },
	{ id: 6, type: 'member', img: 'image/back/member2.png', name: '4Jéssica S.', online: false, group: 4 },
];
const groups = [
	{ id: 1, type: 'group', img: 'image/back/group.png', name: 'Grupo 01', members: 'Darell, Jéssica', num: 1 },
	{ id: 2, type: 'group', img: 'image/back/group.png', name: 'Grupo 02', members: 'Darell, Jéssica', num: 2 },
	{ id: 3, type: 'group', img: 'image/back/group.png', name: 'Grupo 03', members: 'Darell, Jéssica', num: 3 },
];

const ChatBtn = () => {
	const dispatch = useDispatch()

	const showChat = useSelector(state => state.common.showChat)
	const isChat = useSelector(state => state.common.chatting)

	const [chatCount, setChatCount] = useState(0);
	const [openChatLists, setOpenChatList] = useState([]);

	const exitChat = () => {
		window.dragon.unityKeyboard()
		dispatch(chatting(false))
	}

	// const memberClick = (id) => {
	// 	const memberCheck = openChatLists.filter((el) => {
	// 		return el.id === id && el.type === 'member';
	// 	});
	// 	if (memberCheck.length !== 0 || chatCount >= 1) return
	// 	const newMArray = { type: 'member', id: id }
	// 	let newMChats = [...openChatLists, {
	// 		...newMArray
	// 	}];
	// 	setChatCount((oldCount) => oldCount + 1);
	// 	setOpenChatList([...newMChats])
	// 	console.log(openChatLists)
	// }
	const memberClick = (id) => {
		const memberCheck = openChatLists.filter((el) => {
			return el.id === id && el.type === 'member';
		});
		if (memberCheck.length !== 0 || chatCount >= 1) return
		const newMArray = [{ type: 'member', id: id }]
		setOpenChatList([...newMArray])
	}

	// const groupClick = (id, num) => {
	// 	const groupCheck = openChatLists.filter((el) => {
	// 		return el.id === id && el.type === 'group';
	// 	});
	// 	if (groupCheck.length !== 0 || chatCount >= 1) return
	// 	const newGArray = { type: 'group', id: id }
	// 	let newGChats = [...openChatLists, {
	// 		...newGArray
	// 	}];
	// 	setChatCount((oldCount) => oldCount + 1);
	// 	setOpenChatList([...newGChats])
	// 	console.log(openChatLists)
	// }
	const groupClick = (id, num) => {
		const groupCheck = openChatLists.filter((el) => {
			return el.id === id && el.type === 'group';
		})
		if (groupCheck.length !== 0 || chatCount >= 1) return
		const newGArray = [{ type: 'group', id: id }]
		setOpenChatList([...newGArray])
	}

	const exit = (id, type) => {
		var setValue = openChatLists;
		const dataRemoved = setValue.filter((el) => {
			return el.id !== id || el.type !== type;
		});
		setChatCount((oldCount) => oldCount - 1);
		setOpenChatList([...dataRemoved])

	}

	const reduceChat = () => {
		setChatCount((oldCount) => oldCount - 1);
	}

	return (
		<>
			{
				showChat &&
				<div className={styles.chats}>
					<div className={styles.chats_container}>
						{
							openChatLists.map((openChatList, idx) => {
								if (openChatList.type == 'member') {
									let obj = members.filter(item => item.id === openChatList.id)
									return <Chatmain value={obj[0]} exit={exit} key={idx} />
								}
								if (openChatList.type == 'group') {
									let obj = groups.filter(item => item.id === openChatList.id)
									return <Chatmain value={obj[0]} exit={exit} key={idx} />
								}
							})
						}
						{(
							isChat ?
								<Chatlist exitChat={exitChat} members={members} groups={groups} memberClick={memberClick} groupClick={groupClick} />
								:
								<div className={styles.home_chat_img_container} onClick={() => dispatch(chatting(true))}>
									<img className={styles.home_chat_img} src={getImg('image/icons/chat_btn.png')} alt="Logo" />
								</div>
						)}

					</div>
				</div>
			}
		</>
	)
}

export default ChatBtn