import React, { useEffect, useState } from "react";
import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import Badge from '@mui/material/Badge';
import { styled } from '@mui/material/styles';

import styles from './Forumitem.module.scss';
import { getImg } from "../../../utils/Helper";

const StyledBadge = styled(Badge)(({ theme }) => ({
	'& .MuiBadge-badge': {
		left: 27,
		top: 7,
		border: 'none',
		padding: '0',
		backgroundColor: 'transparent',
		transform: `scale(1) translate(0, -41%)`
	},
}));

const img = <img className={styles.active} src={getImg('image/icons/forum_active.png')} alt="avatar" />

const Forumitem = (props) => {

	return (
		<>
			<div className={(!props.lastview ? styles.forum : `${styles.forum} ${styles.lastview}`)}>
				<div className={styles.forum_container}>
					{
						props.value.active &&

						<StyledBadge badgeContent={img}>
							<img className={`${styles.forum_avatar} ${styles.avatar_active}`} src={props.value.avatarImg} alt="avatar" />
						</StyledBadge>
					}
					{
						!props.value.active &&
						<img className={styles.forum_avatar} src={props.value.avatarImg} alt="avatar" />
					}

					<div className={styles.forum_content}>
						<div className={styles.content_name}>{props.value.name}</div>
						<div className={styles.content}>{props.value.content}</div>
						<div className={styles.control_btn_container}>
							{
								props.value.status === 1 &&
								<>
									<div className={`${styles.control_btn} ${styles.mar_r_8} ${styles.active}`} onClick={() => props.updateStatus(props.index, 0)}><ThumbUpIcon sx={{ fontSize: 13 }} /></div>
									<div className={styles.control_btn}><ThumbDownIcon sx={{ fontSize: 13 }} onClick={() => props.updateStatus(props.index, -1)}/></div>
								</>
							}
							{
								props.value.status === 0 &&
								<>
									<div className={`${styles.control_btn} ${styles.mar_r_8}`} onClick={() => props.updateStatus(props.index, 1)}><ThumbUpIcon sx={{ fontSize: 13 }} /></div>
									<div className={styles.control_btn} onClick={() => props.updateStatus(props.index, -1)}><ThumbDownIcon sx={{ fontSize: 13 }} /></div>
								</>
							}
							{
								props.value.status === -1 &&
								<>
									<div className={`${styles.control_btn} ${styles.mar_r_8}`} onClick={() => props.updateStatus(props.index, 1)}><ThumbUpIcon sx={{ fontSize: 13 }} /></div>
									<div className={`${styles.control_btn} ${styles.active}`} onClick={() => props.updateStatus(props.index, 0)}><ThumbDownIcon sx={{ fontSize: 13 }} /></div>
								</>
							}
						</div>
					</div>
				</div>
			</div>
		</>
	)
}

export default Forumitem