import React, { useEffect, useState, useRef } from "react";

import styles from './Progress.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";



const Progress = (props) => {

	return (
		<div className={styles.progress}>
			<div className={styles.back} style={{ backgroundColor: props.back }}></div>
			{
				props.value === 100 ?
					<div className={styles.bar100} style={{ backgroundColor: props.bar, width: props.value + '%' }}></div>
					:
					<div className={styles.bar} style={{ backgroundColor: props.bar, width: props.value + '%' }}></div>
			}
			<div className={styles.text}>{props.value >= 0 ? props.value : 0}%</div>
		</div>
	)
}

export default Progress