import React, { useEffect, useState } from "react";
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

import Marketitem from "../Marketitem";
import UserCard from "../UserCard";
import styles from './Pagination.module.scss';
let emptyDiv = [];

const Pagination = (props) => {
	const [page, setPage] = useState(1)
	const [totalPageCount, setTotalPageCount] = useState(0)
	const [empty, setEmpty] = useState([])

	const makeEmpty = () => {
		emptyDiv = [];
		const count = props.count - (props.items.length % props.count === 0 ? props.count : props.items.length % props.count);

		// console.log("props count: ", props.count)
		// console.log("count: ", count)

		for (let i = 0; i < count; i++) {
			if (props.type === 1) {
				emptyDiv.push(<div className={styles.empty_marketing_div}></div>)
			} else {
				emptyDiv.push(<div className={styles.empty_user_div}></div>)
			}
		}
		setEmpty([...emptyDiv])
	}

	const prev = () => {
		if(totalPageCount === 0 || totalPageCount === 1) return
		setPage((oldPage) => (oldPage !== 1 ? oldPage - 1 : 1))
	}
	const next = () => {
		if(totalPageCount === 0 || totalPageCount === 1) return
		const pageCount = Math.ceil(props.items.length / props.count / props.rows.length)
		if (page + 1 === pageCount) {
			makeEmpty();
		}
		setPage((oldPage) => (oldPage !== pageCount ? oldPage + 1 : oldPage))
	}

	useEffect(() => {
		const pc = Math.ceil(props.items.length / props.count / props.rows.length)
		if(props.items.length === 0) {
			setTotalPageCount(0)
		} else {
			setTotalPageCount(pc)
		}
		if (page === pc) {
			makeEmpty();
		}
	}, [props.items])
	return (
		<>
			<div className={styles.pagination}>
				<div className={styles.pagination_container}>
					{
						props.rows.map((row, index) => {
							let c = 0;
							if (props.rows.length === index + 1) {
								return <div className={`${styles.last_item_container} ${(page === Math.ceil(props.items.length / props.count / props.rows.length)) && styles.max_page}`} key={index}>
									{props.items.map((item, idx) => {
										c = 0
										if (idx >= ((page - 1) * props.rows.length + index) * props.count && idx < ((page - 1) * props.rows.length + index + 1) * props.count && props.type === 1) {
											c++;
											return <Marketitem value={item} key={idx} />
										} else if (idx >= ((page - 1) * props.rows.length + index) * props.count && idx < ((page - 1) * props.rows.length + index + 1) * props.count && props.type === 2) {
											c++;
											return <UserCard value={item} key={idx} index={idx} typeIndex={props.typeIndex} activeInstructor={props.activeInstructor} />
										}
									})}
									{
										c !== 0 && page == Math.ceil(props.items.length / props.count / props.rows.length) && empty
									}
								</div>
							} else {
								return <div className={styles.item_container} key={index}>
									{props.items.map((item, idx) => {
										c = 0
										if (idx >= ((page - 1) * props.rows.length + index) * props.count && idx < ((page - 1) * props.rows.length + index + 1) * props.count && props.type === 1) {
											c++;
											return <Marketitem value={item} key={idx} handleClick={props.handleClick} />
										} else if (idx >= ((page - 1) * props.rows.length + index) * props.count && idx < ((page - 1) * props.rows.length + index + 1) * props.count && props.type === 2) {
											c++;
											return <UserCard value={item} key={idx} index={idx} typeIndex={props.typeIndex} activeInstructor={props.activeInstructor} />
										}
									})}
									{
										c !== 0 && page == Math.ceil(props.items.length / props.count / props.rows.length) && empty
									}
								</div>
							}
						})
					}

					<div className={styles.control_btn_container}>
						<div className={styles.display}>
							{(props.items.length === 0 ? 0 : (page - 1) * props.count * props.rows.length + 1)}-
							{
								page * props.count * props.rows.length <= props.items.length ?
									page * props.count * props.rows.length :
									props.items.length
							}
						</div>
						<div className={styles.control_btn} onClick={() => prev()}>
							<ArrowBackIosNewIcon sx={{ fontSize: 15 }} />
						</div>
						<div className={styles.control_btn} onClick={() => next()}>
							<ArrowForwardIosIcon sx={{ fontSize: 15 }} />
						</div>
					</div>
				</div>
			</div>
		</>
	)
}

export default Pagination