import React, { useEffect, useState, useRef } from "react";
import { useSelector, useDispatch } from 'react-redux'
import CloseIcon from '@mui/icons-material/Close';
import ZoomInIcon from '@mui/icons-material/ZoomIn';

import Winoption from "../Chat/Winoption";
import Member from "../Chat/Member";
import Group from "../Chat/Group";

import styles from './Chatlist.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";



const Chatlist = (props) => {

	const auth = useSelector(state => state.auth)

	const [groupNum, setGroupNum] = useState(1);
	const [searchVal, setSearchVal] = useState('');
	const [avatarImg, setAvatarImg] = useState('');

	const clickGroup = (g) => {
		setGroupNum(g);
	}
	const minimize = () => {

	}
	const restore = () => {

	}

	useEffect(() => {
		let modelData = auth.characterData
		if(modelData.gendor !== -1) {
			const imageData = localStorage.getItem('avatar3_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin)
			if(imageData != undefined && imageData != null)
				setAvatarImg(imageData)
		}
	}, [auth.characterData])

	return (
		<div className={styles.chatlist}>
			<div className={styles.chatlist_container}>
				<div className={styles.chatlist_user_info_container}>
					{/* <Winoption exit={props.exitChat} minimize={minimize} restore={restore} chatList={true} /> */}
					<div className={styles.close_btn} onClick={() => props.exitChat()}>
						<CloseIcon className={styles.close_icon}/>
					</div>
					<div className={styles.chatlist_user_info}>
						<div className={styles.user_avatar_container}>
							{
								avatarImg === '' ?
									<img className={styles.user_avatar} src={getImg('image/back/avatar.png')} alt="Logo" />
								:
									<img className={styles.user_avatar} src={"data:image/png;base64," + avatarImg} alt="Logo" />
							}
						</div>
						<div className={styles.user_name}>
							{auth.currentUser.firstname}
						</div>
					</div>
					<div className={styles.chatlist_search_container}>
						<div className={styles.search_input_container}>
							<input className={styles.search_input} value={searchVal} onFocus={() => window.dragon.reactKeyboard()} onBlur={() => window.dragon.unityKeyboard()} onChange={(e) => setSearchVal(e.target.value)} />
							<div className={styles.search_input_icon}>
								<img className={styles.search_icon} src={getImg('image/icons/search_light.png')} alt="Logo" />
							</div>
						</div>
					</div>
				</div>
				<div className={styles.chatlist_content}>
					<div className={styles.chatlist_member_list_container}>
						<div className={styles.member_list_title}>
							Amigos
						</div>
						<div className={styles.member_list_container}>
							{props.members.map((member, idx) => {
								if (member.group === groupNum) {
									if ((searchVal !== '' && member.name.toLowerCase().search(searchVal.toLowerCase()) !== -1) || searchVal === '') {
										return <Member value={member} key={idx} memberClick={props.memberClick} />
									}
								}
							})}
						</div>
					</div>
					<div className={styles.chatlist_divider_container}>
						<div className={styles.chatlist_divider}></div>
					</div>
					<div className={styles.chatlist_group_list_container}>
						<div className={styles.chatlist_group_list_title}>
							Grupos
						</div>
						<div className={styles.chatlist_group_list}>
							{props.groups.map((group, idx) => <Group value={group} key={idx} clickGroup={clickGroup} groupClick={props.groupClick} />)}
						</div>
					</div>
				</div>
			</div>
			<div className={styles.chatlist_bottom_container}>
				<img className={styles.chatlist_bottom_back_img} src={getImg('image/back/chatlist_bottom_back.png')} alt="back" />
				<div className={styles.chatlist_exit_btn_container}>
					<button className={styles.chatlist_exit_btn} onClick={() => props.exitChat()}>
						<CloseIcon />
					</button>
				</div>
			</div>
		</div>
	)
}

export default Chatlist