import React, { useState, useRef, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'

import AchievementAcc from "../../components/AchievementAcc";
import AchievementItem from "../../components/AccordionItem/AchievementItem";
import CertificationAcc from "../../components/CertificationAcc";
import CertificationItem from "../../components/AccordionItem/CertificationItem";

import styles from './Progress.module.scss';
import { getImg, searchInObject } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from "../../actions/auth";
import agent from '../../api/'



const Progress = (props) => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const auth = useSelector(state => state.auth)


  const [search, setSearch] = useState('');
  const [oAchievements, setOAchievements] = useState([]);
  const [oCertifications, setOCertifications] = useState([]);
  const [achievements, setAchievements] = useState([]);
  const [certifications, setCertifications] = useState([]);
  const [image, setImage] = useState('');

  useEffect(() => {


    async function getProgressData() {
      dispatch(startAction())
      try {
        const resAchievement = await agent.common.getAchievement(auth.currentUser.id)
        const rescertification = await agent.common.getCertification(auth.currentUser.id)
        if (resAchievement.data.success) {
          setOAchievements([...resAchievement.data.data])
          setAchievements([...resAchievement.data.data])
        }
        if (rescertification.data.success) {
          setOCertifications([...rescertification.data.data])
          setCertifications([...rescertification.data.data])
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getProgressData()

    let modelData = auth.characterData
    setImage(localStorage.getItem('avatar2_' + modelData.gendor + modelData.hair + modelData.face + modelData.clothes + modelData.shoes + modelData.skin))
  }, [auth.characterData])

  useEffect(() => {
    if (search !== '') {
      const a = searchInObject(oAchievements, search)
      const c = searchInObject(oCertifications, search)
      setAchievements([...a])
      setCertifications([...c])
    } else {
      setAchievements([...oAchievements])
      setCertifications([...oCertifications])
    }
  }, [search])

  useEffect(() => {
    console.log("length", achievements.length)
  }, [achievements])



  return (
    <div className={styles.progress_content}>
      <div className={styles.progress_content_container}>
        <div className={styles.header}>
          <div className={styles.title}>
            Progresso
          </div>
          <div className={styles.progress_search}>
            <div className={styles.search_input_container}>
              <input value={search} onChange={(e) => setSearch(e.target.value)} className={styles.search_input} placeholder="Pesquisar" />
              <div className={styles.input_icon}>
                <img className={styles.search_icon} src={getImg('image/icons/search_dark.png')} alt="Logo" />
              </div>
            </div>
          </div>
        </div>
        <div className={styles.content}>
          <div className={styles.left_content}>
            <div className={styles.rel_container}>
              <div className={styles.model_img}>
                {
                  image === '' ?
                    <img src={getImg('image/back/progress_back.png')} alt="Logo" />
                    :
                    <img src={"data:image/png;base64," + image} alt="Logo" />
                }
              </div>
            </div>
            <div className={styles.effect}></div>
          </div>
          <div className={styles.right_content}>
            <div className={styles.title}>
              Conquistas
            </div>
            <div className={styles.achievement_item_content}>
              {
                achievements.length === 0 ?
                  <div className={styles.no_result}>No Result</div> :
                  achievements.map((achievement, idx) =>
                    <AchievementAcc value={achievement} content={<AchievementItem value={achievement.content} />} key={idx} />
                  )
              }
            </div>
          </div>
        </div>
        {
          certifications.length > 0 && 
          <div className={styles.certification}>
            <div className={styles.title}>
              Certificados
            </div>
            {
              certifications.map((certification, idx) =>
                <CertificationAcc value={certification} content={<CertificationItem link={certification.linkAddress} />} key={idx} />
              )
            }
          </div>
        }
      </div>
    </div>
  )
}

export default Progress