import React from "react";
import MinimizeIcon from '@mui/icons-material/Minimize';
import WebAssetIcon from '@mui/icons-material/WebAsset';
import CloseIcon from '@mui/icons-material/Close';

import styles from './Winoption.module.scss';

const Winoption = (props) => {

	return (
		<div className={styles.winoption}>
			<div className={styles.winoption_container}>
				{/* <div className={styles.option_btn} onClick={() => props.minimize()}>
					<MinimizeIcon sx={{ fontSize: 14 }} />
				</div>
				<div className={styles.option_btn} onClick={() => props.restore()}>
					<WebAssetIcon sx={{ fontSize: 14 }} />
				</div> */}
				<div className={styles.option_btn} onClick={() => { if (props.chatList) { props.exit() } else { props.exit(props.id, props.type) } }}>
					<CloseIcon sx={{ fontSize: 14 }} />
				</div>
			</div>
		</div >
	)
}

export default Winoption