import React from "react";

import styles from './Group.module.scss';
import { getImg } from "../../../utils/Helper";
const Group = (props) => {

	return (
		<div className={styles.group} onClick={() => { props.clickGroup(props.value.num); props.groupClick(props.value.id, props.value.num) }}>
			<div className={styles.group_container}>
				<div className={styles.group_avatar}>
					<img className={styles.avatar} src={getImg(`${props.value.img}`)} alt="Logo" />
				</div>
				<div className={styles.group_info_container}>
					<div className={styles.group_name}>
						{props.value.name}
					</div>
					<div className={styles.group_members}>
						{props.value.members}
					</div>
				</div>
			</div>
		</div>
	)
}

export default Group