import React, { useEffect, useState } from "react";
import FolderOutlinedIcon from '@mui/icons-material/FolderOutlined';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import CloudDownloadOutlinedIcon from '@mui/icons-material/CloudDownloadOutlined';
import { saveAs } from "file-saver";

import styles from './CourseItem.module.scss';

const label = { inputProps: { 'aria-label': 'Checkbox demo' } };

const CourseItem = (props) => {

	const [showAction, setShowAction] = useState(false);
	const [fileList, setFileList] = useState([]);

	const saveFile = (f_url, f_name) => {
		saveAs(
			f_url,
			f_name
		);
	};

	useEffect(() => {
		let fileArray = []
		props.value.map((item) => {
			item.files.map((file) => {
				if (file.type === 'pdf') {
					fileArray.push({ name: item.title + '-config.pdf', downloadLink: file.url })
				} else if (file.type === 'zip') {
					fileArray.push({ name: item.title + '-documentos.zip', downloadLink: file.url })
				} else {
					fileArray.push({ name: item.title + '-download.' + file.type, downloadLink: file.url })
				}
			})
		})

		setFileList([...fileArray])
	}, [])


	return (
		<div className={styles.course}>
			{props.value.map((item, idx) =>
				<div className={styles.course_item} key={idx}>
					<div className={styles.first}>
						<div className={styles.check}>
							{
								item.status !== 0 && <input className={styles.course_check} type="checkbox" defaultChecked onChange={() => props.checkVideo(props.courseId, idx, item.id)} />
							}
							{
								item.status === 0 && <input className={styles.course_check} type="checkbox" onChange={() => props.checkVideo(props.courseId, idx, item.id)} />
							}
						</div>
						<div className={styles.title} onClick={() => props.changeVideo(item.video)}>
							{item.title}
						</div>
					</div>
					<div className={styles.last}>
						{item.time}
					</div>
				</div>
			)}
			<div className={styles.action_btn_container}>
				{!showAction &&
					<div className={styles.rel_container}>
						<div className={styles.action_btn} onClick={() => setShowAction((oldShowAction) => oldShowAction ? false : true)}>
							Recursos
							<FolderOutlinedIcon className={styles.folder_icon} />
							<KeyboardArrowDownIcon className={styles.up_icon} />
						</div>
					</div>
				}
				{showAction &&
					<>
						<div className={styles.rel_container}>
							<div className={styles.action_btn} onClick={() => setShowAction((oldShowAction) => oldShowAction ? false : true)}>
								Recursos
								<FolderOutlinedIcon className={styles.folder_icon} />
								<KeyboardArrowUpIcon className={styles.up_icon} />
							</div>
						</div>
						<div className={styles.rel_container}>
							<div className={styles.action}>
								{
									fileList.map((item, idx) =>
										<div className={styles.download_btn} key={idx} onClick={() => saveFile(item.downloadLink, item.name)}>
											<CloudDownloadOutlinedIcon className={styles.download_icon} />
											{item.name}
										</div>
									)
								}
							</div>
						</div>
					</>
				}
			</div>


		</div>
	)
}

export default CourseItem