import React, { useEffect, useState } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import NotificationsIcon from '@mui/icons-material/Notifications'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'

import {
	changePage,
	changePageId,
	startAction,
	endAction,
	showToast
} from '../../../../actions/common'
import { logout } from "../../../../actions/auth"
import agent from '../../../../api'

import styles from './NotificationItem.module.scss'

const NotificationItem = (props) => {

	const auth = useSelector(state => state.auth)
	const navigate = useNavigate()
	const dispatch = useDispatch()

	const setRead = async () => {
		if (!props.value.viewed) {
			dispatch(startAction())
			const res = await agent.common.setReadNotificationById(auth.currentUser.id, props.value.id)
			if (res.data.success) dispatch(showToast('success', res.data.message))
			else dispatch(showToast('error', res.data.message))
			dispatch(endAction())
		}
	}

	const goPage = (page, id) => {
		dispatch(changePageId(id))
		dispatch(changePage(page))
	}

	return (
		<div className={`${styles.notification_item} ${(!props.value.viewed && styles.not_read)}`}>
			<div className={styles.title}>
				{props.value.title}
			</div>
			<div className={styles.content}>
				{props.value.description}
			</div>

			<div className={styles.btn_container} onClick={() => setRead()}>
				{
					props.value.type === 1 &&
					<a href={props.value.link} target="_blank">
						Ir para conteúdos
						<ArrowForwardIosIcon />
					</a>
				}
				{
					props.value.type === 2 &&
					<a href={props.value.link} target="_blank">
						Ir para aula ao vivo
						<ArrowForwardIosIcon />
					</a>
				}
				{
					props.value.type === 3 &&
					<a href={props.value.link} target="_blank">
						Ir para forúm
						<ArrowForwardIosIcon />
					</a>
				}
			</div>

		</div>
	)
}

export default NotificationItem