import React, { useState, useEffect } from "react"
import { Link, useNavigate } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import moment from 'moment'

import { Calendar } from "react-multi-date-picker"
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos'

import "react-multi-date-picker/styles/colors/red.css"


import Sidemenu from "../../components/Sidemenu"
import ScheduleItem from "../../components/ScheduleItem"
import ChatBtn from "../../components/ChatBtn"


import './Custom.css'
import styles from './Schedule.module.scss'
import { getImg, useWindowSize } from "../../utils/Helper"

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from "../../actions/auth"
import agent from '../../api/'

const months = ["Janeiro", "fevereiro", "março", "abril", "maio", "junho", "julho", "agosto", "setembro", "Outubro", "novembro", "dezembro"]

const Schedule = (props) => {
  
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const [windowWidth, windowHeight] = useWindowSize();
  const [containerWidth, setContainerWidth] = useState(0);
  const [value, setValue] = useState([new Date()]);
  const [paramDate, setParamDate] = useState([moment(new Date()).format('YYYY/MM/DD')]);
  const [events, setEvents] = useState([]);
  const [oEvents, setOEvents] = useState([]);
  const [search, setSearch] = useState('');
  const [firstLoadCalendar, setFirstLoadCalendar] = useState(true);
  


  const divHeight = windowHeight - 262;
  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width}px)`);
  }

  const changeMonth = (e) => {
    console.log(months[e.month.index])
    var monthElements = document.getElementsByClassName('rmdp-header-values')
    monthElements[0].children[0].innerText = months[e.month.index]
    // for (let i = 0; i < monthElements.length; i++) {
    //   monthElements[i].children[0].innerText = monthElements[i].children[0].innerText.replace(/,/g, '')
    // }
  }

  useEffect(() => {
    if (firstLoadCalendar) {
      var monthElements = document.getElementsByClassName('rmdp-header-values')
      if (monthElements.length != 0) {
        for (let i = 0; i < monthElements.length; i++) {
          monthElements[i].children[0].innerText = monthElements[i].children[0].innerText.replace(/,/g, '')
        }
        setFirstLoadCalendar(false)
      }
    }
  })

  useEffect(() => {
    const getData = async () => {
      dispatch(startAction())
      try {
        const resSchedule = await agent.common.getEvents()
        if (resSchedule.data.success) {
          let eventArray = [...resSchedule.data.data]
          eventArray.sort(function (a, b) {
            return new Date(a.date) - new Date(b.date)
          })

          let dateArray = []
          eventArray.map((item) => {
            dateArray.push(moment(item.date).format('YYYY/MM/DD'))
          })

          setParamDate([...dateArray])

          setEvents([...eventArray])
          setOEvents([...eventArray])
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()

  }, [])

  useEffect(() => {
    if (search !== '') {
      let newArray = []
      oEvents.map((event) => {
        if (event.title.search(search) !== -1 || event.name.search(search) !== -1 || event.subtitle.search(search) !== -1) {
          newArray.push(event)
        }
      })
      setEvents([...newArray])
    } else {
      setEvents([...oEvents])
    }
  }, [search])

  const handleChange = (e) => {
    let param = [];
    let valueArray = [];
    e.map((item) => {
      let newDate = new Date(item.year + '-' + (item.monthIndex + 1) + '-' + item.day)
      valueArray.push(newDate)
      param.push(moment(newDate).format('YYYY/MM/DD'))
    })
    setParamDate([...param])
    setValue([...valueArray])
  }

  return (
    <div className={styles.main}>
      <div className={styles.schedule}>
        <Sidemenu getWidth={getWidth} active={4} />
        <div className={styles.schedule_container} style={{ width: containerWidth }}>
          <div className={styles.left_section}>
            <div className={styles.header}>
              Agenda
            </div>
            <div className={styles.subtitle}>
              Calendário
            </div>
            <div className={styles.calendar_container} style={{ height: divHeight + 'px' }}>
              <Calendar
                className="custom-calendar red"
                value={paramDate}
                multiple
                hideWeekDays={true}
                months={months}
                disableMonthPicker={true}
                disableYearPicker={true}
                showOtherDays={true}
                // onChange={(e) => handleChange(e)}
                onChange={() => console.log("paramDate=", paramDate)}
                onMonthChange={(e) => changeMonth(e)}
              />
            </div>
          </div>
          <div className={styles.right_section}>
            <div className={styles.header}>
              <div className={styles.search_input_container}>
                <input className={styles.search_input} value={search} onChange={(e) => setSearch(e.target.value)} placeholder="Pesquisar evento" />
                <div className={styles.input_icon}>
                  <img className={styles.search_icon} src={getImg('image/icons/search_dark.png')} alt="Logo" />
                </div>
              </div>
            </div>
            <div className={styles.subtitle}>
              Eventos
            </div>
            <div className={styles.rel_container} style={{ height: divHeight + 'px' }}>
              <div className={styles.schedule_items} >
                {
                  events.length === 0 ?
                    <div className={styles.no_result}>No Result</div> :
                    events.map((event, idx) => <ScheduleItem value={event} key={idx} />)
                }
              </div>
            </div>
          </div>
          {/* <ChatBtn members={members} groups={groups} user={user} /> */}
        </div>
      </div>
    </div>
  )
}

export default Schedule