import React, { useEffect, useState } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import Badge from '@mui/material/Badge';
import NotificationsIcon from '@mui/icons-material/Notifications';

import styles from './Menuitem.module.scss';
import { getImg } from "../../../utils/Helper";
import { logout } from "../../../actions/auth";
import { changePage } from "../../../actions/common";

const Menuitem = (props) => {

	const dispatch = useDispatch()

	const goPage = () => {
		props.closeInterval()
		dispatch(changePage(props.value.page))
	}

	return (
		<>
			{props.small &&
				<div className={styles.small_menuitem}>
					{props.active &&
						<button onClick={() => goPage()} className={`${styles.small_menuitem_btn} ${styles.active}`}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
					{!props.active && props.value.id !== 8 && props.value.id !== 6 && props.value.id !== 1 && props.value.id !== 9 &&
						<>
							{
								props.value.page !== '' ?
									<button onClick={() => goPage()} className={styles.small_menuitem_btn}>
										<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
									</button>
									:
									<button className={styles.small_menuitem_btn}>
										<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
									</button>
							}
						</>
					}
					{props.value.id === 8 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.toggleNotification()}>
							<Badge className={styles.badge} badgeContent={props.notification}>
								<NotificationsIcon />
							</Badge>
						</button>
					}
					{
						props.value.id === 6 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.getCustomizeData()}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
					{
						props.value.id === 1 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.goLectureRoom()}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
					{
						props.value.id === 9 &&
						<button className={styles.small_menuitem_btn} onClick={() => props.logoutClick()}>
							<img className={styles.small_menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
						</button>
					}
				</div>
			}
			{!props.small &&
				<div className={styles.menuitem}>
					{props.active &&
						<div className={styles.hover_btn}>
							<button onClick={() => goPage()} className={`${styles.menuitem_btn} ${styles.active}`}>
								{props.value.id !== 8 &&
									<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								}
								{props.value.text}
							</button>
							<div className={`${styles.left_btn} ${styles.active}`}>
							</div>
						</div>
					}
					{!props.active && props.value.id !== 8 && props.value.id !== 6 && props.value.id !== 1 && props.value.id !== 9 &&
						<div className={styles.nohover_btn}>
							{
								props.value.page !== '' ?
									<>
										<button onClick={() => goPage()} className={styles.menuitem_btn}>
											<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
											{props.value.text}
										</button>
										<div className={styles.left_btn}>
										</div>
									</>
									:
									<>
										<button className={styles.menuitem_btn}>
											<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
											{props.value.text}
										</button>
										<div className={styles.left_btn}>
										</div>
									</>
							}

						</div>
					}
					{props.value.id === 8 &&
						<div className={styles.nohover_btn} onClick={() => props.toggleNotification()}>
							<button className={styles.menuitem_btn}>
								<Badge className={styles.badge} badgeContent={props.notification}>
									<NotificationsIcon />
								</Badge>
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
					{
						props.value.id === 6 &&
						<div className={styles.nohover_btn}>
							<button className={styles.menuitem_btn} onClick={() => props.getCustomizeData()}>
								<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
					{
						props.value.id === 1 &&
						<div className={styles.nohover_btn}>
							<button className={styles.menuitem_btn} onClick={() => props.goLectureRoom()}>
								<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
					{
						props.value.id === 9 &&
						<div className={styles.nohover_btn}>
							<button className={styles.menuitem_btn} onClick={() => props.logoutClick()}>
								<img className={styles.menuitem_icon} src={getImg(`${props.value.img}`)} alt="menuitem" />
								{props.value.text}
							</button>
							<div className={styles.left_btn}>
							</div>
						</div>
					}
				</div>
			}
		</>
	)
}

export default Menuitem