import React, { useState, useRef, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'

import CloseIcon from '@mui/icons-material/Close';

import styles from './NonePage.module.scss'


import agent from '../../api/'

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout, createCharacter } from '../../actions/auth'

const NonePage = () => {

  const navigate = useNavigate()
  const dispatch = useDispatch()
  const auth = useSelector(state => state.auth)

  const [isShow, setIsShow] = useState(false)

  window.dragon.setCustomizeData = function (background, gendor, clothes, hair, shoes, face, skin) {
    const setData = async (background, gendor, clothes, hair, shoes, face, skin) => {
      try {
        const res = await agent.common.setCustomizeModel(
          "" + auth.currentUser.id,
          "" + background,
          "" + gendor,
          "" + clothes,
          "" + hair,
          "" + shoes,
          "" + face,
          "" + skin
        )
        if (res.data.success) {
          const modelData = {
            background: background,
            gendor: gendor,
            hair: hair,
            face: face,
            clothes: clothes,
            shoes: shoes,
            skin: skin
          }
          const modelEyeData = {
            data: {
              background: background,
              gendor: gendor,
              hair: hair,
              face: face,
              eye: 1,
              clothes: clothes,
              shoes: shoes,
              skin: skin
            }
          }
          dispatch(createCharacter(modelEyeData))

          console.log("character data=", auth.characterData)

          window.dragon.makeCharacter(modelData)
          window.dragon.sendCharacterData(1)

          setIsShow(true)
          dispatch(showToast('success', res.data.message))
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    setData(background, gendor, clothes, hair, shoes, face, skin)
  }

  useEffect(() => {
  }, [])

  window.dragon.goHomePage = function () {
    dispatch(changePage('home'))
  }

  window.dragon.saveImage0 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar0_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  window.dragon.saveImage1 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar1_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  window.dragon.saveImage2 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar2_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  window.dragon.saveImage3 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar3_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
  }

  return (
    <>
      {
        isShow &&
        <div className={styles.main}>
          <div className={styles.message_card}>
            <div className={styles.rel_container}>
              <div className={styles.message}>
                Seu Avatar foi modificado com sucesso!
              </div>
              <div className={styles.close} onClick={() => setIsShow(false)}>
                <CloseIcon className={styles.close_btn} />
              </div>
            </div>
          </div>
        </div>
      }
    </>
  )
}

export default NonePage