import React, { useEffect, useState } from "react";
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'

import styles from './Marketitem.module.scss';
import { getImg } from "../../../utils/Helper";

import { changePage, changePageId } from "../../../actions/common";

const Marketitem = (props) => {

	const dispatch = useDispatch()

	const [hourTime, setHourTime] = useState(0)

	const goPage = (page, id) => {
		dispatch(changePageId(id))
		dispatch(changePage(page))
	}

	useEffect(() => {
		const timeArray = props.value.time.split(":")
		let h = 0
		let m = 0
		let s = 0
		if (timeArray.length > 0) {
			h = parseInt(timeArray[0])
		}
		if (timeArray.length > 1) {
			m = parseInt(timeArray[1])
		}
		if (timeArray.length > 2) {
			s = parseInt(timeArray[2])
		}

		// console.log('h=', timeArray[0])
		// console.log('m=', timeArray[1])
		// console.log('s=', timeArray[2])

		// setHourTime( h * 3600 + m * 60 + s )
		setHourTime(((h * 3600 + m * 60 + s) / 3600).toFixed(1))
	}, [])

	return (
		<div className={styles.market} onClick={() => goPage('detail', props.value.id)}>
			<div className={styles.market_img_container}>
				<img className={styles.market_img} src={props.value.courseImg} alt="Logo" />
			</div>
			<div className={styles.market_content_container}>
				<div className={styles.content_title}>
					{props.value.title}
				</div>
				<div className={styles.content}>
					{props.value.content}
				</div>
			</div>
			<div className={styles.subcontent_container}>
				<div className={styles.subcontent}>
					{props.value.count} Aulas&nbsp;&nbsp;·&nbsp;&nbsp;{hourTime} horas no total&nbsp;&nbsp;·&nbsp;&nbsp;{props.value.text}
				</div>
			</div>
		</div>
	)
}

export default Marketitem