import React, { useState } from "react";
import CloseIcon from '@mui/icons-material/Close';
import useMouse from '@react-hook/mouse-position'

import Winoption from "../Chat/Winoption";
import Message from "../Chat/Message";

import styles from './Chatmain.module.scss';
import { getImg, useMousePos } from "../../utils/Helper";

let msgs = [
	{ img: 'image/back/avatar.png', name: 'Letícia M.', chat: 'Olá, tudo bem?', time: '17:20', type: 'send' },
	{ img: 'image/back/member1.png', name: 'Darell B.', chat: 'Tudo sim e com você?', time: '17:24', type: 'receive' },
	{ img: 'image/back/avatar.png', name: 'Letícia M.', chat: 'Olá, tudosdfhsdrhgsrgdrfges ersgse gsergse sgrg srg serg srgs erg ser serg serg srgser gsr gse ge rgs er gser', time: '17:30', type: 'send' }
];
let add_msg = { img: 'image/back/avatar.png', name: 'Letícia M.', chat: '', time: '', type: 'send' };
let rel_x = 0;
let rel_y = 0;
let x = 0;
let y = 0;
const Chatmain = (props) => {

	const { posX, posY } = useMousePos();
	const [message, setMessage] = useState('');
	const [move, setMove] = useState(false);
	const [mini, setMini] = useState(false);
	const [moveStyle, setMoveStyle] = useState(null);
	const [chats, setChats] = useState([]);

	const minimize = () => {
		setMini(true);
	}

	const restore = () => {
		setMini(false);
	}

	const addMessage = () => {
		var current = new Date();
		add_msg.chat = message;
		add_msg.time = current.getHours() + ':' + current.getMinutes();
		let newChats = [...chats, {
			...add_msg
		}];
		setChats([...newChats])
		setMessage('')
	}

	const mouseDown = (e) => {
		if (e.button !== 0) return
		setMove(true);
		rel_x = e.nativeEvent.offsetX;
		rel_y = e.nativeEvent.offsetY;
		e.stopPropagation()
		e.preventDefault()
	}

	const mouseUp = (e) => {
		setMove(false);
		e.stopPropagation()
		e.preventDefault()
	}

	const mousemove = (e) => {
		if (!move) return
		x = e.pageX - rel_x
		y = e.pageY - rel_y
		const styleArray = {
			position: 'fixed',
			top: y,
			left: x
		}
		setMoveStyle({ ...styleArray });

		e.stopPropagation()
		e.preventDefault()
	}

	const msgKeyPress = (e) => {
		if (e.key === 'Enter') {
			addMessage()
		}
	}
	return (
		<>
			{
				!mini &&
				<div className={styles.chatmain} style={moveStyle}>
					<div className={styles.chatmain_container}>
						<div className={styles.chatmain_header} onMouseMove={(e) => mousemove(e)} onMouseDown={(e) => mouseDown(e)} onMouseUp={(e) => mouseUp(e)} onMouseLeave={(e) => mouseUp(e)}>
							<div className={styles.chatmain_header_container}>
								<Winoption exit={props.exit} minimize={minimize} restore={restore} id={props.value.id} type={props.value.type} />
								<div className={styles.user_tab}>
									<div className={styles.user_tab_container}>
										<div className={styles.user_tab_content}>
											<img className={styles.user_tab_avatar} src={getImg(`${props.value.img}`)} alt="Logo" />
											<div className={styles.user_tab_name}>
												{props.value.name}
											</div>
												<div className={styles.user_tab_close_btn_container} onClick={() => props.exit(props.value.id, props.value.type)}>
												<CloseIcon sx={{ fontSize: 10 }} />
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
						<div className={styles.chat_content}>
							<div className={styles.content}>
								<div className={styles.chat_content_container}>
									{
										chats.slice(0).reverse().map((chat, idx) => <Message value={chat} key={idx} />)
									}
								</div>
								<div className={styles.chat_input_container}>
									<div className={styles.chat_input}>
											<input className={styles.input} value={message} onFocus={() => window.dragon.reactKeyboard()} onBlur={() => window.dragon.unityKeyboard()} onChange={(e) => setMessage(e.target.value)} onKeyPress={(e) => msgKeyPress(e)} placeholder="Insira sua mensagem..." />
										<div className={styles.chat_three_dot}>...</div>
										<img className={styles.chat_send_btn} onClick={() => addMessage()} src={getImg('image/icons/send.png')} alt="send" />
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			}
			{
				mini &&
				<div className={`${styles.chatmain} ${styles.minimize}`} style={moveStyle}>
					<div className={styles.chatmain_container}>
						<div className={styles.chatmain_header} onMouseMove={(e) => mousemove(e)} onMouseDown={(e) => mouseDown(e)} onMouseUp={(e) => mouseUp(e)}>
							<div className={styles.chatmain_header_container}>
								<Winoption exit={props.exit} minimize={minimize} restore={restore} id={props.value.id} type={props.value.type} />
								<div className={styles.user_tab}>
									<div className={styles.user_tab_container}>
										<div className={styles.user_tab_content}>
											<img className={styles.user_tab_avatar} src={getImg(`${props.value.img}`)} alt="Logo" />
											<div className={styles.user_tab_name}>
												{props.value.name}
											</div>
											<div className={styles.user_tab_close_btn_container}>
												<CloseIcon sx={{ fontSize: 10 }} />
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			}
		</>
	)
}

export default Chatmain