import React, { useState, useRef, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom';


import Pagination from "../../components/University/Pagination";

import styles from './UniversityInstructor.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
	changePage,
	startAction,
	endAction,
	showToast
} from '../../actions/common'
import { logout } from "../../actions/auth";
import agent from '../../api/'

const contentsMenu =
	[
		'Todos os cursos',
		'Marketing e Vendas',
		'Treinamento de Vendas',
		'Endomarketing',
		'Gestão de Tempo',
		'Gestão Financeira',
		'Digitação',
		'Produtividade',
		'Design Thinking'
	];

const UniversityInstructor = (props) => {
	const auth = useSelector(state => state.auth)
	const dispatch = useDispatch()
	const navigate = useNavigate()
	const [windowWidth, windowHeight] = useWindowSize();


	const [paginationShowCount, setPaginationShowCount] = useState(0);
	const [searchVal, setSearchVal] = useState('');
	const [instructors, setInstructors] = useState([]);
	const [oInstructors, setOInstructors] = useState([]);
	const contentRef = useRef(null);

	const getCount = () => {
		const contentWidth = contentRef.current?.offsetWidth;
		const count = Math.floor(contentWidth / 245);
		setPaginationShowCount(count);
	}

	const activeInstructor = async (type, idx, val) => {
		dispatch(startAction())
		const res = await agent.common.updateFollow(auth.currentUser.id, instructors[type].data[idx].id, (val ? 1 : 0))
		if (res.data.success) dispatch(showToast('success', res.data.message))
		else dispatch(showToast('error', res.data.message))
		dispatch(endAction())

		let oldArray = instructors
		oldArray[type].data[idx].followStauts = val
		setInstructors([...oldArray])
	}

	const getInstuctors = (a) => {
		let typeArray = [];
		for (let i = 0; i < a.length; i++) {
			let count = 0;
			for (let j = 0; j < typeArray.length; j++) {
				if (a[i].type === typeArray[j]) count++
			}
			if (!count) typeArray.push(a[i].type)
		}
		let instruct = []
		for (let k = 0; k < typeArray.length; k++) {
			let typeInstructor = []
			a.map((instructor) => {
				if (instructor.type === typeArray[k]) {
					typeInstructor.push(instructor)
				}
			})
			instruct.push({ type: typeArray[k], data: [...typeInstructor] })
		}
		return instruct
	}


	useEffect(() => {
		const getData = async () => {
			dispatch(startAction())
			try {
				const resInstructors = await agent.common.getInstructors()
				if (resInstructors.data.success) {
					setInstructors([...getInstuctors(resInstructors.data.data)])
					setOInstructors([...getInstuctors(resInstructors.data.data)])
				}
				dispatch(endAction())
			} catch (error) {
				if (error.response.status >= 400 && error.response.status <= 415) {
					dispatch(endAction())
					dispatch(showToast('Failed', error.response.data.message))
					if (error.response.data.message == 'Unauthorized') {
						localStorage.removeItem('token')
						dispatch(logout())
						window.dragon.stopUnityGame()
						dispatch(changePage('login'))
					}
				}
			}
		}
		getData()
	}, [])

	useEffect(() => {
		if (searchVal !== '') {
			let newArray = []
			oInstructors.map((instructor) => {
				instructor.data.map((element) => {
					if (element.name.search(searchVal) != -1 || `${element.sudentCount}`.search(searchVal) != -1 || contentsMenu[Number(element.type)].search(searchVal) != -1) {
						newArray.push(element)
					}
				})
			})
			setInstructors([...getInstuctors(newArray)])
		} else {
			setInstructors([...oInstructors])
		}
	}, [searchVal])

	return (
		<div className={styles.university_content} style={{ minHeight: `${windowHeight - 265}px` }}>
			<div className={styles.tab_btn_container}>
				<div className={styles.tab_btn}>
					<button className={`${styles.tab_first_btn} ${styles.r_back}`} onClick={() => dispatch(changePage('university'))}>Conteúdos</button>
					<button className={`${styles.tab_second_btn} ${styles.w_back}`}>Instrutores</button>
				</div>
			</div>
			<div className={styles.university_instructor} ref={contentRef} onLoad={() => getCount()}>
				<div className={styles.header}>
					<div className={styles.title}>
						Instrutores
					</div>
					<div className={styles.search_input_container}>
						<input className={styles.search_input} value={searchVal} onChange={(e) => setSearchVal(e.target.value)} placeholder="Pesquisar" />
						<div className={styles.input_icon}>
							<img className={styles.search_icon} src={getImg('image/icons/search_dark.png')} alt="Logo" />
						</div>
					</div>
				</div>
				{
					instructors.length === 0 ?
						<div className={styles.no_result}>No Result</div> :
						instructors.map((instructor, index) => {
							return (
								<div className={styles.market} key={index}>
									<div className={styles.title}>
										{contentsMenu[instructor.type]}
									</div>
									<div className={styles.content}>
										<Pagination activeInstructor={activeInstructor} typeIndex={index} items={instructor.data} count={paginationShowCount} rows={['']} type={2} />
									</div>
								</div>
							)
						}
						)
				}
			</div >
		</div>

	)
}

export default UniversityInstructor