import React, { useState, useEffect, useRef } from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import InfiniteScroll from "react-infinite-scroll-component";
import PersonIcon from '@mui/icons-material/Person';

import Sidemenu from "../../components/Sidemenu";
import RankingItem from "../../components/RankingItem";

import styles from './Ranking.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import { logout } from '../../actions/auth'
import agent from '../../api/'

let count = 0

const Ranking = () => {
  const dispatch = useDispatch()

  const rankingRef = useRef(null);

  const [containerWidth, setContainerWidth] = useState(0)
  const [rankings, setRankings] = useState([])
  const [showRankings, setShowRankings] = useState([])
  const [makeRankingAvatar, setMakeRankingAvatar] = useState(false)
  const [showTopBtn, setShowTopBtn] = useState(false)
  const [hasMore, setHasMore] = useState(true)
  // const [hasMe, setHasMe] = useState(false)

  const user = useSelector((state) => (state.auth.currentUser))

  useEffect(() => {
    async function getRankingData() {
      dispatch(startAction())
      try {
        count = 0
        let rankingArray = []
        const resRanking = await agent.common.getRanking()
        if (resRanking.data.success) {

          setHasMore(resRanking.data.data.length > 100)
          setRankings([...resRanking.data.data])
          console.log('end setRankings')
          resRanking.data.data.map((rankingData, idx) => {
            if(idx < 100) {
              // if(rankingData.customizeInfo != null && rankingData.customizeInfo.id_user === user.id) {
              //   setHasMe(true)
              // }
              rankingArray.push(rankingData)
            }
            if (rankingData.customizeInfo != null) {
              const c = {
                background: rankingData.customizeInfo.background,
                gendor: rankingData.customizeInfo.gendor,
                hair: rankingData.customizeInfo.hair,
                face: rankingData.customizeInfo.face,
                clothes: rankingData.customizeInfo.clothes,
                shoes: rankingData.customizeInfo.shoes,
                skin: rankingData.customizeInfo.skin
              }
              if (localStorage.getItem('avatar3_' + c.gendor + c.hair + c.face + c.clothes + c.shoes + c.skin) == undefined || localStorage.getItem('avatar3_' + c.gendor + c.hair + c.face + c.clothes + c.shoes + c.skin) == null) {
                window.dragon.captureCharacter(c)
              } else {
                count++
              }
            } else {
              count++
            }
          })

          setShowRankings([...rankingArray])
          console.log('end useEffect')
        }
        dispatch(endAction())
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(endAction())
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getRankingData()
  }, [])

  useEffect(() => {
    if (count == rankings.length && count != 0) {
      console.log('makerankingavatar=', true + '  ' + count + '   ' + rankings.length)
      setMakeRankingAvatar(true)
    } else {
      console.log('makerankingavatar=', false + '  ' + count + '   ' + rankings.length)
      setMakeRankingAvatar(false)
    }
  }, [count])

  // useEffect(() => {
  //   if(hasMe) {
  //     window.location.href = "#me"
  //   }
  // }, [hasMe])

  useEffect(() => {
    setShowTopBtn(rankingRef.current?.scrollTop >= 100)
  }, [rankingRef.current?.scrollTop])

  const fetchMoreData = () => {
    setTimeout(() => {
      let allRankingsArray = rankings
      let showRankingsArray = showRankings
      if(allRankingsArray.length > showRankingsArray.length){

        let page = showRankingsArray.length / 100
        for (let i = showRankingsArray.length; i < (page+1)*100; i++) {
          showRankingsArray.push(allRankingsArray[i])
        }
        console.log("show ranking length=", showRankingsArray.length)
        // if(!hasMe) {
        //   showRankingsArray.map((item) => {
        //     if(item.customizeInfo != null && item.customizeInfo.id_user === user.id) {
        //       setHasMe(true)
        //     }
        //   })
        // }
        setShowRankings([...showRankingsArray])
      } else {
        setHasMore(false)
      }
    }, 1500);
  }

  const userFunction = () => {
    window.location.href = "#me"
  }

  const topFunction = () => {
    rankingRef.current?.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  }

  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width}px)`);
  }

  window.dragon.saveImage3 = function (data, gendor, hair, face, clothes, shoes, skin) {
    localStorage.setItem('avatar3_' + gendor + hair + face + clothes + shoes + skin, encodeURIComponent(data));
    count++
  }

  // When the user clicks on the button, scroll to the top of the document



  return (
    <div className={styles.main}>
      <div className={styles.ranking}>
        <Sidemenu getWidth={getWidth} active={5} />
        <div ref={rankingRef} id='ranking' className={styles.ranking_container} style={{ width: containerWidth }}>
          <div className={styles.header}>
            <img src={getImg('image/icons/ranking_header.png')} alt="Logo" />
            <div className={styles.title}>
              Ranking
            </div>
          </div>
          <div className={styles.ranking_table}>
            <div className={styles.table_header}>
              <div className={styles.name}>Nome</div>
              <div className={styles.unit}>Unidade</div>
              <div className={styles.office}>Cargo</div>
              <div className={styles.time}>Tempo</div>
              <div className={styles.achivement}>Conquistas</div>
              <div className={styles.average}>Média</div>
            </div>
            <div className={styles.table_content}>
              <InfiniteScroll
                dataLength={showRankings.length}
                next={fetchMoreData}
                hasMore={hasMore}
                scrollableTarget="ranking"
              >
                {showRankings.map((item, idx) => {
                  if (item?.customizeInfo?.id_user === user.id) {
                    return <RankingItem value={item} makeRankingAvatar={makeRankingAvatar} me={true} order={idx + 1} key={idx} />
                  } else if (idx % 2 === 1) {
                    return <RankingItem value={item} makeRankingAvatar={makeRankingAvatar} even={true} order={idx + 1} key={idx} />
                  } else {
                    return <RankingItem value={item} makeRankingAvatar={makeRankingAvatar} even={false} order={idx + 1} key={idx} />
                  }
                })}
              </InfiniteScroll>
            </div>
            
          </div>

        </div>
        <div className={styles.userbtn_container}>
          <button onClick={() => userFunction()} title="Go to user">
            <PersonIcon  sx={{ fontSize: 30 }}/>
          </button>
        </div>
        {
          showTopBtn &&
          <div className={styles.topbtn_container}>
            <button onClick={() => topFunction()} title="Go to top">Top</button>
          </div>
        }
      </div>
    </div>
  )
}

export default Ranking