import React, { useEffect, useState, useRef } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedinIn, faFacebookF, faInstagram } from '@fortawesome/free-brands-svg-icons';
import { faLink } from '@fortawesome/fontawesome-free-solid';
import CloseIcon from '@mui/icons-material/Close';

import CourseComplete from "../CourseComplete";
import Timer from '../Timer';
import styles from './Test.module.scss';
import { getImg, useWindowSize, useInterval } from "../../utils/Helper";

import agent from '../../api/'

import {
  changePage,
  startAction,
  endAction,
  showToast,
  isLive
} from '../../actions/common'
import { logout, createCharacter } from '../../actions/auth'

const questions = [
	{
		question: '1.qual é cor principal da coca cola?',
		exactAnswerNum: 0,
		time: 30,
		answers: ['verde', 'amarelo', 'rosa', 'roxo', 'vermelho']
	},
	{
		question: '2.qual é cor principal da coca cola?',
		exactAnswerNum: 0,
		time: 30,
		answers: ['verde', 'amarelo', 'rosa', 'roxo', 'vermelho']
	}
]

const answer = ['verde', 'amarelo', 'rosa', 'roxo', 'vermelho']
var intervalId

const Test = (props) => {
	const auth = useSelector(state => state.auth)

  const dispatch = useDispatch()

	const [windowWidth, windowHeight] = useWindowSize();

	const [show, setShow] = useState(true)
	const [showMark, setShowMark] = useState(false)
	const [questionNum, setQuestionNum] = useState(0)
	const [answerNum, setAnswerNum] = useState(0)
	const [mark, setMark] = useState(0)
	const [progress, setProgress] = useState(1)
	const [questions, setQuestions] = useState([])

	const checkQuestion = async () => {
		clearInterval(intervalId)
		try {
			const res = await agent.common.getAnswerUser(questions[questionNum].id)
			if (res.data.success) {
				res.data.data.map((item) => {
					if(item.id === answerNum) {
						if(item.correct) {
							setMark((old) => old + 5 / questions.length)
						}
					}
				})

				if (questionNum < questions.length - 1) {
					setAnswerNum(questions[questionNum + 1].answers[0].id)
					setQuestionNum((old) => old + 1)
				} else {
					setShow(false)
					setShowMark(true)
					localStorage.setItem("courseTest" + auth.currentUser.id + "-" + props.courseId, true)
				}
			}
		} catch (error) {
			if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
				dispatch(showToast('error', error.response.data.message))
				if (error.response.data.message == 'Unauthorized') {
					localStorage.removeItem('token')
					dispatch(logout())
					window.dragon.stopUnityGame()
					dispatch(changePage('login'))
				}
			}
		}
	}

	useEffect(() => {
		const getQuestion = async () => {
      try {
        const res = await agent.common.getCourseQuestionUser(props.courseId)
        if (res.data.success) {
          let questionsArray = []
					res.data.data.map((item) => {
						let rimAnswer = []
						item.courseQuestionAnswer.map((answer) => {
							rimAnswer.push({id: answer.id, title: answer.title})
						})
						let rimQuestion = {
							id: item.id,
							courseId: item.id_course,
							question: item.title,
							answers: rimAnswer
						}
						questionsArray.push(rimQuestion)
					})
					setQuestions([...questionsArray])
					setAnswerNum(questionsArray[0].answers[0].id)
        }
      } catch (error) {
        if (error.response !== undefined && error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            dispatch(logout())
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getQuestion()
	}, [])

	useEffect(() => {
		setProgress(30)
		intervalId = setInterval(function () {
			setProgress((oldProgress) => oldProgress <= 0 ? 0 : oldProgress - 1)
		}, 1000)
	}, [questionNum])

	useEffect(() => {
		if (progress == 0) {
			checkQuestion()
		}
	}, [progress])


	return (
		<>
			{
				show &&
				<div className={styles.complete_modal} style={{ width: windowWidth + 'px', height: windowHeight + 'px' }}>
					<div className={styles.card}>
						<div className={styles.card_title}>
							bem vindo - vamos testar o teu conhecimento?
						</div>
						<div className={styles.card_question_container}>
							<div className={styles.card_question}>
								{questions[questionNum] !== null && questions[questionNum] !== undefined && questions[questionNum].question}
							</div>
							<div className={styles.card_timer}>
								<Timer time={30} progress={progress} />
							</div>
						</div>
						<div className={styles.card_answer_container}>
							{
								questions[questionNum] !== null && questions[questionNum] !== undefined &&
								questions[questionNum].answers.map((item, idx) => {
									return (
										<div className={styles.answer_container} key={idx}>
											<div className={styles.check}>
												<input id={"answer" + idx} className={styles.answer_check} defaultChecked={!idx ? true : false} type="radio" name="radio-group" onChange={() => setAnswerNum(item.id)} />
												<label htmlFor={"answer" + idx}>{item.title}</label>
											</div>
										</div>
									)
								})
							}
						</div>
						<div className={styles.card_confirm_btn} onClick={() => checkQuestion()}>
							Confirmar
						</div>
					</div>
				</div>
			}
			{
				showMark && mark != 5 &&
				<div className={styles.complete_modal} style={{ width: windowWidth + 'px', height: windowHeight + 'px' }}>
					<div className={styles.mark_card}>
						<div className={styles.rel_container}>
							<div className={styles.text}>
								Your Score is {mark}.
							</div>
							<div className={styles.close_button} onClick={() => setShowMark(false)}>
								<CloseIcon />
							</div>
						</div>
					</div>
				</div>
			}
			{
				showMark && mark == 5 && <CourseComplete />
			}
		</>
	)
}

export default Test