import React, { useState, useEffect } from "react";
import { useDispatch } from 'react-redux'
import { useNavigate, Link } from 'react-router-dom'


import styles from './Login.module.scss';
import { useWindowSize } from "../../utils/Helper";

import agent from '../../api/'

import {
  changePage,
  changePageId,
  startAction,
  endAction,
  showToast,
  startGame
} from '../../actions/common'

import { login, createCharacter } from '../../actions/auth'

const Login = (props) => {

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [windowWidth, windowHeight] = useWindowSize();

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [emailInputError, setEmailInputError] = useState(false)
  const [passwordInputError, setPasswordInputError] = useState(false)

  const { RtcTokenBuilder, RtmTokenBuilder, RtcRole, RtmRole } = require('agora-access-token')

  const submitLogin = async () => {
    email === '' ? setEmailInputError(true) : setEmailInputError(false)
    password === '' ? setPasswordInputError(true) : setPasswordInputError(false)
    if (email !== '' && password !== '') {

      const reEmail = convertParameter(email)
      const rePassword = convertParameter(password)

      let res
      dispatch(startAction())
      try {
        res = await agent.auth.login(reEmail, rePassword)
        dispatch(endAction())
        if (res.data.success) {
          localStorage.setItem('token', res.data.token)
          // const resModel = await agent.common.getCustomizeModel(res.data.data.id)
          dispatch(showToast('success', res.data.message))
          console.log('auth in dispatch', res.data)
          dispatch(login(res.data))
          dispatch(startGame())
          dispatch(changePage('home'))
        }
      } catch (error) {
        if (error.response != undefined) {
          if (error.response.status >= 400 && error.response.status <= 415) {
            dispatch(endAction())
            dispatch(showToast('error', error.response.data.message))
          }
        }
      }

      try {
        // const res = await agent.auth.login(reEmail, rePassword)
        // dispatch(endAction())
        // if (res.data.success) {
        // localStorage.setItem('token', res.data.token)
        const resModel = await agent.common.getCustomizeModel(res.data.data.id)
        // dispatch(showToast('success', res.data.message))
        // dispatch(login(res.data))
        // dispatch(startGame())
        if (resModel.data.success) {
          dispatch(createCharacter(resModel.data))
        }
        // dispatch(changePage('home'))
        // }
      } catch (error) {
        if (error.response != undefined) {
          if (error.response.status >= 400 && error.response.status <= 415) {
            dispatch(endAction())
            dispatch(showToast('error', error.response.data.message))
          }
        }
      }
    }
  }

  const convertParameter = (param) => {
    let convertParam = '';
    for (let i = 0; i < param.length; i++) {
      if (param.charAt(i) == '#') {
        convertParam += '%23'
      } else if (param.charAt(i) == '&') {
        convertParam += '%26'
      } else {
        convertParam += param.charAt(i)
      }
    }

    return convertParam;
  }

  // useEffect(() => {
  //   const appID = '6195d75b94514a34bf1ba7fde0397979';
  //   const appCertificate = 'a00a92ddeea74486aed03dd33887a8f4';
  //   const channelName = 'coke';
  //   const uid = 2882341273;
  //   const account = "2882341273";
  //   const role = RtcRole.PUBLISHER;

  //   const expirationTimeInSeconds = 3600

  //   const currentTimestamp = Math.floor(Date.now() / 1000)

  //   const privilegeExpiredTs = currentTimestamp + expirationTimeInSeconds

  //   // IMPORTANT! Build token with either the uid or with the user account. Comment out the option you do not want to use below.

  //   // Build token with uid
  //   const tokenA = RtcTokenBuilder.buildTokenWithUid(appID, appCertificate, channelName, uid, role, privilegeExpiredTs);
  //   console.log("Token With Integer Number Uid: " + tokenA);

  //   // Build token with user account
  //   // const tokenB = RtcTokenBuilder.buildTokenWithAccount(appID, appCertificate, channelName, account, role, privilegeExpiredTs);
  //   // console.log("Token With UserAccount: " + tokenB);
  // }, [])


  return (
    <div className={styles.main}>
      <div className={styles.login}>
        <div className={styles.login_card}>
          <div className={styles.login_title}>
            Entre com sua conta empresarial
          </div>
          <div className={styles.login_subtitle}>
            Você precisa estar logado para ter acesso ao Universo
          </div>
          <input className={`${styles.login_input} ${styles.email} ${(emailInputError ? styles.error : '')}`} value={email} onChange={(e) => setEmail(e.target.value)} onKeyPress={(e) => { if (e.keyCode === 13 || e.charCode === 13) submitLogin() }} placeholder="Insira seu e-mail empresarial" />
          <input className={`${styles.login_input} ${styles.password}  ${(passwordInputError ? styles.error : '')}`} value={password} onChange={(e) => setPassword(e.target.value)} onKeyPress={(e) => { if (e.keyCode === 13 || e.charCode === 13) submitLogin() }} placeholder="Insira sua senha" type="password" />
          {/* <div className={styles.login_forgot_btn_container}>
            <div className={styles.login_forgot_btn} onClick={() => dispatch(changePage('signup'))}>Esqueceu a senha?</div>
          </div> */}
          <div className={styles.login_confirm_btn_container}>
            <button id="submit_login" onClick={() => submitLogin()} className={styles.login_confirm_btn}>Iniciar Sessão</button>
          </div>
        </div>
      </div>
    </div >
  )
}

export default Login