import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { createPortal } from 'react-dom'

import { getImg } from "../../../utils/Helper";
import styles from './CertificationItem.module.scss';

const CertificationItem = (props) => {

	return (
		<div className={styles.certification_item}>
			<div className={styles.header}>
				<div className={styles.link_address}>
					Acesse o link para download:&nbsp;&nbsp;
					<a href={props.link} target='_blank'>
						{props.link}
					</a>
				</div>
				<div className={styles.social_container}>
					<div className={styles.social_text}>
						Compartilhe nas Redes Sociais:
					</div>
					<div className={styles.icons}>
						<div className={styles.social_icon}>
							<img src={getImg('image/icons/facebook.png')} alt="Logo" />
						</div>
						<div className={styles.social_icon}>
							<img src={getImg('image/icons/instagram.png')} alt="Logo" />
						</div>
					</div>
				</div>
			</div>
			<div className={styles.link_content}>
				<iframe src={props.link} className={styles.iframe_link}></iframe>
				{/* <iframe src="http://speedwellclient.com" className={styles.iframe_link} title="example"></iframe> */}
			</div>
		</div>
	)
}

export default CertificationItem