import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from 'react-redux'
import { Outlet, useNavigate } from "react-router-dom";
import Circle from 'react-circle';

import Sidemenu from "../../components/Sidemenu";

import styles from './UniversityLayout.module.scss';
import { getImg, useWindowSize } from "../../utils/Helper";

import {
  changePage,
  startAction,
  endAction,
  showToast
} from '../../actions/common'
import agent from '../../api/'

const UniversityLayout = (props) => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const auth = useSelector(state => state.auth)

  const [windowWidth, windowHeight] = useWindowSize();
  const [containerWidth, setContainerWidth] = useState(0);
  const [notification, setNotification] = useState(111);
  const [progress, setProgress] = useState(0);

  const getWidth = (width) => {
    setContainerWidth(`calc(100% - ${width - 28}px)`);
  }

  useEffect(() => {
    async function getData() {
      try {
        const res = await agent.common.getProgress(auth.currentUser.id)
        if (res.data.success) {
          if (res.data.data.progress == null || res.data.data.progress == NaN || res.data.data.progress == "NaN") {
            setProgress(0)
          } else {
            setProgress(parseFloat(res.data.data.progress))
          }
        }
      } catch (error) {
        if (error.response.status >= 400 && error.response.status <= 415) {
          dispatch(showToast('error', error.response.data.message))
          if (error.response.data.message == 'Unauthorized') {
            localStorage.removeItem('token')
            window.dragon.stopUnityGame()
            dispatch(changePage('login'))
          }
        }
      }
    }
    getData()
  }, [])

  return (
    <div className={styles.main}>
      <div className={styles.university}>
        <Sidemenu getWidth={getWidth} notification={notification} active={props.active} />
        <div className={styles.university_container} style={{ width: containerWidth }}>
          <div className={styles.university_rel_container}>
            <div className={styles.university_header}>
              <div className={styles.university_header_title}>
                <div className={styles.header_title}>
                  <img className={styles.header_title_img} src={getImg('image/back/text_img.png')} alt="Logo" />
                </div>
                <div className={styles.header_content}>
                  Bem-vindo ao seu ambiente de estudos
                </div>
                <div className={styles.header_progress_container}>
                  <div className={styles.progress_container}>
                    <Circle
                      animate={true}
                      animationDuration="1s"
                      responsive={false}
                      size="102"
                      lineWidth="50"
                      progress={`-${progress}`}
                      progressColor="#E53222"
                      bgColor="#FDF2D0"
                      textColor="#FFF"
                      roundedStroke={true}
                      showPercentage={true}
                      showPercentageSymbol={true}
                    />
                    <div className={styles.percent_text}>
                      {progress.toFixed(0)}%
                    </div>
                  </div>
                  <div className={styles.progress_text}>
                    Progresso
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.red_back}>
              {props.component}
            </div>
          </div>
        </div>
      </div>
    </div >
  )
}

export default UniversityLayout