import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate } from 'react-router'

import agent from '../api/'

import { login, createCharacter } from '../actions/auth'

import { changePage, startGame, showChat } from '../actions/common'

const NonProtectedRoute = props => {
  const dispatch = useDispatch()
  const [token, setToken] = useState(localStorage.getItem('token'))
  const auth = useSelector(state => state.auth)
  useEffect(() => {
    localStorage.removeItem('token')
    async function getAuthInfo() {
      if (!auth.currentUser.email) {
        try {
          const res = await agent.auth.me()
          if (res.data.success) {
            const resModel = await agent.common.getCustomizeModel(res.data.data.id)
            dispatch(login(res.data))
            if (resModel.data.success) {
              dispatch(createCharacter(resModel.data))
            }
          }

        } catch (error) {
          if (error.response != undefined && error.response.status >= 400 && error.response.status <= 415) {
            localStorage.removeItem('token')
            setToken('')
          }
        }
      }
    }
    getAuthInfo()
    dispatch(showChat(false))
  }, [])
  // if (auth.currentUser.email) {
  //   dispatch(startGame())
  //   return dispatch(changePage('home'))
  // }
  if (!token) {
    return <React.Fragment>{props.children}</React.Fragment>
  }
  return <></>
}

export default NonProtectedRoute