import React, { useEffect, useRef, useState } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay } from '@fortawesome/fontawesome-free-solid'

import styles from './Video.module.scss';

function Video(props) {
	const videoRef = useRef(null);

	// const videoWidth = videoRef.current?.offsetWidth;
	const [playing, setPlaying] = useState(false);
	const [hover, setHover] = useState(false);
	const [currentTime, setCurrentTime] = useState(0);
	const [videoTime, setVideoTime] = useState(0);
	const [progress, setProgress] = useState(0);
	const [controls, setControls] = useState(false);
	const [videoWidth, setVideoWidth] = useState(0);
	let intervalID = useRef(null);

	const stopClick = () => {
		setVideoTime(videoRef.current?.duration)
		setControls(true)
		videoRef.current.play();
	}

	const getWidth = () => {
		const w = videoRef.current?.offsetWidth;
		setVideoWidth(w);
		var vid = document.getElementById("video" + props.id);
		if (props.value.lastTime !== undefined && props.value.lastTime !== null) {
			if (props.value.lastTime == 100) {
				vid.currentTime = 0
			} else {
				const totalTime = videoRef.current?.duration;
				vid.currentTime = totalTime * props.value.lastTime / 100;
				console.log("change current time")
			}
		}
	}

	useEffect(() => {
		// var videos = document.querySelectorAll("video");
		// for (var i = 0, l = videos.length; i < l; i++) {
		// 		var video = videos[i];
		// 		var src = video.src || (function () {
		// 				var sources = video.querySelectorAll("source");
		// 				for (var j = 0, sl = sources.length; j < sl; j++) {
		// 						var source = sources[j];
		// 						var type = source.type;
		// 						var isMp4 = type.indexOf("mp4") != -1;
		// 						if (isMp4) return source.src;
		// 				}
		// 				return null;
		// 		})();
		// 		if (src) {
		// 				var isYoutube = src && src.match(/(?:youtu|youtube)(?:\.com|\.be)\/([\w\W]+)/i);
		// 				if (isYoutube) {
		// 						var id = isYoutube[1].match(/watch\?v=|[\w\W]+/gi);
		// 						id = (id.length > 1) ? id.splice(1) : id;
		// 						id = id.toString();
		// 						var mp4url = "http://www.youtubeinmp4.com/redirect.php?video=";
		// 						video.src = mp4url + id;
		// 				}
		// 		}
		// }
	}, [props.value.video])

	useEffect(() => {
		let callCount = [0, 0]
		if (controls) {
			intervalID.current = setInterval(function () {
				if (videoRef.current?.paused) {
					if (videoRef.current?.currentTime == videoRef.current?.duration) {
						props.setVideoTime(100)
						props.setIsPause(true)
						setControls(false)
						var vid = document.getElementById("video" + props.id);
						if (vid !== null) vid.currentTime = 0;
					}
					if (!callCount[0]) {
						props.setVideoTime((videoRef.current?.currentTime * 100 / videoRef.current?.duration).toFixed(2))
						props.setIsPause(true)
						callCount[0]++
						callCount[1] = 0
					}
				} else {
					if (!callCount[1]) {
						props.setIsPause(false)
						callCount[1]++
						callCount[0] = 0
					}

				}
			}, 1000);
		} else {
			clearInterval(intervalID.current);
		}
	}, [controls])

	return (
		<div className={styles.video}>
			<div className={styles.source_container}>
				{
					props.width !== undefined && props.width !== null &&
					<video
						style={{ width: props.width + 'px' }}
						id={`video${props.id}`}
						ref={videoRef}
						className={`${styles.source} ${(props.value.lastTime == 100 ? styles.progress : styles.no_progress)}`}
						src={props.value.video || 'https://data-1.utreon.com/v/Y2/I1/N2/rPSHGCnIiUg/rPSHGCnIiUg_original.mp4'}
						controls={controls}
						onLoadedData={() => getWidth()}
						type="video/mp4"
					></video>
				}
				{
					props.height !== undefined && props.height !== null &&
					<video
						style={{ height: props.height + 'px' }}
						id={`video${props.id}`}
						ref={videoRef}
						className={`${styles.source} ${(props.value.lastTime == 100 ? styles.progress : styles.no_progress)}`}
						src={props.value.video || 'https://data-1.utreon.com/v/Y2/I1/N2/rPSHGCnIiUg/rPSHGCnIiUg_original.mp4'}
						controls={controls}
						onLoadedData={() => getWidth()}
					></video>
				}

				{!controls && videoWidth !== 0 &&
					<>
						<div className={styles.source_hover_img} style={{ width: videoWidth + 'px' }}></div>
						<div className={styles.stop_btn_container} onClick={() => stopClick()}>
							<FontAwesomeIcon icon={faPlay} />
						</div>
					</>
				}
			</div>

		</div >
	);
}

export default Video;
